import React from "react";
import { useDispatch } from "react-redux";
import { useHistory } from "react-router";
import { ParseDate } from "../../../../helpers/helper";
import Swal from "sweetalert2";
import {
  currentEditGuia,
  deleteGuia,
} from "../../../../redux/actions/guias_actions";
import "../crear-guias/crearGuias.css";

function ItemListaCreadas({ guia }) {
  const dispatch = useDispatch();
  const history = useHistory();
  const swalWithBootstrapButtons = Swal.mixin({
    customClass: {
      confirmButton: "btn btn-danger mb-2 w-40",
      cancelButton: "btn btn-success mb-2 w-40 mx-2",
      title:"text-alert text-start",
    },
    buttonsStyling: false,
  });
  const showAlet = () => {
    return swalWithBootstrapButtons
      .fire({
        title: "Estas seguro de eliminar la guía?",
        text: `Esta guia se eliminara de forma permanente, no se podra recuperar los datos ingresados`,
        showCancelButton: true,
        confirmButtonText: "Eliminar",
        cancelButtonText: "No",
        reverseButtons: true,
      })
      .then((result) => {
        if (result.isConfirmed) {
          dispatch(deleteGuia(guia.idGuia));
        }
      });
  };

  return (
    <tr className="hover-table  ">
      <td className="num-Guia-item"> {guia.remitente.nombres} </td>
      <td className="num-Guia-item"> {guia.destinatario.nombres} </td>
      <td> {ParseDate(guia.fechaActual)} </td>
      <td> {`${guia.remitente.provincia}-${guia.remitente.canton}`} </td>
      <td> {`${guia.destinatario.provincia}-${guia.destinatario.canton}`} </td>
      <td>
        <div
          type="button"
          className=""
          onClick={() => dispatch(currentEditGuia(guia, history))}
        >
          <i className="large material-icons">created</i>
        </div>
      </td>
      <td>
        <div type="button" className=" " onClick={showAlet}>
          <i className="large material-icons">delete_forever</i>
        </div>
      </td>
    </tr>
  );
}

export default ItemListaCreadas;
