import { SET_CURRENT_PAGE, SET_SUB_CURRENT_PAGE } from "../../types/types";

const initialState = {
  page: "",
  subPage: "",
};

export const pagesReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_CURRENT_PAGE:
      return {
        ...state,
        page: action.payload,
      };
    case SET_SUB_CURRENT_PAGE:
      return {
        ...state,
        subPage: action.payload,
      };
    default:
      return state;
  }
};
