import "../actualizar_pesos/actualizarPesos.css";
import React from "react";
import { ParseDate } from "../../../../helpers/helper";
import Spinner from "../../../spinner/spinner";
import Dashfooter from "../../../dash-componets/footter/dashfooter";
import ActualizarPesosHook from "../../../../hooks/Actualizar_Pesos_Hook";
import { DebounceInput } from "react-debounce-input";

function ActualizarPesos() {
  const [
    ListGuiasParaPeso,
    values,
    fetching,
    currentDetailGuiaPesos,
    ListGuiasPesoActualizado,
    updateGuiaonList,
    eliminardeListaActualizados,
    editGuiaPeso,
    cancelarEdit,
    agregarAListaActualizados,
    activatePesoVol,
    handleChangeValue,
    searchGuia,
    updateGuiasFirebase,
  ] = ActualizarPesosHook();

  const {
    idGuia,
    // fechaActual,
    // peso,
    // tipo,
    // numPiezas,
    // remitente,
    // destinatario,
    // contenido,
  } = currentDetailGuiaPesos;

  return (
    <div className="crearGuias col-md-9 ms-sm-auto col-lg-10 px-md-4">
      <div className="container-fluid pt-4">
        <h4 className="title-separtor">Actualizar Pesos</h4>
        <hr />
        <p>{idGuia}</p>
        <div className="card card-body">
          <div className="row align-items-center">
            <div className="col-sm-12 col-md-4">
              <h5 className="titleSection-form">Buscar Guia</h5>
            </div>
            <div className="col-sm-12 col-md-8">
              <form>
                <div className="input-group">
                  <DebounceInput
                    minLength={0}
                    debounceTimeout={500}
                    type="text"
                    className="form-control dropdown-toggle"
                    placeholder="Ingresar Num. Guía"
                    id="top-search"
                    onChange={handleChangeValue}
                    value={values?.idGuia}
                    name="idGuia"
                  />
                  <span className="mdi mdi-magnify search-icon" />
                  {fetching ? (
                    <div className="container-spinner-buscar">
                      <Spinner />
                    </div>
                  ) : (
                    <button
                      className="btn-solid-secondary px-4 "
                      type="button"
                      onWaiting={searchGuia}
                    >
                      Buscar
                    </button>
                  )}
                </div>
              </form>
            </div>
          </div>
          <h5 className="titleSection-form mt-4">Guia Seleccionada</h5>
          <hr />
          <table className="table dt-responsive nowrap">
            <thead>
              <tr className="header-table">
                <th
                  className="overflow-text"
                  data-bs-toggle="tooltip"
                  data-bs-placement="bottom"
                  title="Num. Guia"
                >
                  ID
                </th>
                <th>Fecha</th>
                <th>Origen</th>
                <th>Destino</th>
                {/* <th
                  className="overflow-text"
                  data-bs-toggle="tooltip"
                  data-bs-placement="bottom"
                  title="Ver Detalles"
                >
                  Detalles
                </th> */}
              </tr>
            </thead>

            <tbody>
              {ListGuiasParaPeso?.idGuia === undefined ? (
                <></>
              ) : (
                <tr
                  key={`consultados_${ListGuiasParaPeso?.idGuia}`}
                  className="hover-table"
                >
                  <td
                    className="overflow-text"
                    data-bs-toggle="tooltip"
                    data-bs-placement="bottom"
                    title={ListGuiasParaPeso?.idGuia}
                  >
                    {ListGuiasParaPeso?.idGuia}
                  </td>
                  <td
                    className="overflow-text"
                    data-bs-toggle="tooltip"
                    data-bs-placement="bottom"
                    title={ParseDate(ListGuiasParaPeso?.fechaActual)}
                  >
                    {ParseDate(ListGuiasParaPeso?.fechaActual)}
                  </td>
                  <td
                    className="overflow-text"
                    data-bs-toggle="tooltip"
                    data-bs-placement="bottom"
                    title={`${ListGuiasParaPeso?.remitente.provincia}-${ListGuiasParaPeso?.remitente.canton}`}
                  >
                    {`${ListGuiasParaPeso?.remitente.provincia}-${ListGuiasParaPeso.remitente.canton}`}
                  </td>
                  <td
                    className="overflow-text"
                    data-bs-toggle="tooltip"
                    data-bs-placement="bottom"
                    title={`${ListGuiasParaPeso?.destinatario.provincia}-${ListGuiasParaPeso?.destinatario.canton}`}
                  >
                    {`${ListGuiasParaPeso?.destinatario.provincia}-${ListGuiasParaPeso?.destinatario.canton}`}
                  </td>
                  {/* <td>
                    <div
                      type="button"
                      className="w-100 "
                      onClickCapture={() => verDetaller(guia)}
                      data-bs-toggle="tooltip"
                      data-bs-placement="bottom"
                      title="Ver Detalles"
                    >
                      <i className="material-icons">remove_red_eye</i>
                    </div>
                  </td> */}
                </tr>
              )}
            </tbody>
          </table>
          {ListGuiasParaPeso?.idGuia === undefined ? (
            <p className="text-muted">No hay guías en la lista</p>
          ) : (
            <></>
          )}
          <h5 className="titleSection-form mt-2">Actualizar Peso</h5>
          <hr />
          {values?.onEdit ? (
            <form>
              <div className="row ">
                <div className="col-sm-12 col-md-5">
                  <div className="mb-3">
                    <label className="form-label-input">Agregar Peso</label>

                    <div className="input-group">
                      <input
                        className="form-control"
                        type="text"
                        className="form-control"
                        placeholder="0.00 kg"
                        name="pesototal"
                        onChange={handleChangeValue}
                        value={values?.pesototal}
                      />
                    </div>
                  </div>
                </div>
              </div>
              <div className="align-items-start">
                <div>
                  <div className="row ">
                    <div className="col-sm-12 col-md-4">
                      <div className="mb-3">
                        <label className="form-label-input">Largo</label>
                        <input
                          className="form-control"
                          type="number"
                          placeholder="0.00 Kg"
                          min={0}
                          name="largo"
                          onChange={handleChangeValue}
                          value={values?.largo}
                        />
                      </div>
                    </div>
                    <div className="col-sm-12 col-md-4">
                      <div className="mb-3">
                        <label className="form-label-input">Ancho</label>
                        <input
                          className="form-control"
                          type="number"
                          placeholder="0.00 Kg"
                          min={0}
                          name="ancho"
                          onChange={handleChangeValue}
                          value={values?.ancho}
                        />
                      </div>
                    </div>
                    <div className="col-sm-12 col-md-4">
                      <div className="mb-3">
                        <label className="form-label-input">Alto</label>
                        <input
                          className="form-control"
                          minLength={0}
                          debounceTimeout={500}
                          type="number"
                          placeholder="0.00 Kg"
                          name="alto"
                          min={0}
                          onChange={handleChangeValue}
                          value={values?.alto}
                        />
                      </div>
                    </div>
                  </div>
                  <div className="row mt-2">
                    <div className="col-sm-12 col-md-12">
                      <button
                        type="button"
                        className="btn-solid-secondary w-100 p-2 mb-1"
                        onClick={updateGuiaonList}
                      >
                        Editar Peso
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            </form>
          ) : (
            <form>
              <div className="row ">
                <div className="col-sm-12 col-md-5">
                  <div className="mb-3">
                    <label className="form-label-input">Agregar Peso</label>
                    {values?.pesoVol ||
                      ListGuiasParaPeso?.idGuia === undefined ? (
                      <div className="input-group">
                        <input
                          className="form-control"
                          type="text"
                          className="form-control"
                          placeholder="0.00 kg"
                          name="pesototal"
                          value={values?.pesototal}
                          readOnly
                        />
                        <button
                          className="btn-solid-secondary px-4 "
                          type="button"
                        >
                          Peso
                        </button>
                      </div>
                    ) : (
                      <div className="input-group">
                        <input
                          className="form-control"
                          type="text"
                          className="form-control"
                          placeholder="0.00 kg"
                          name="pesototal"
                          onChange={handleChangeValue}
                          // onKeyDown={addUpdatePesosList}
                          value={values?.pesototal}
                        />
                        <button
                          className="btn-solid-secondary px-4 "
                          type="button"
                          onClickCapture={() => agregarAListaActualizados()}
                        >
                          Agregar Peso
                        </button>
                      </div>
                    )}
                    {ListGuiasParaPeso?.idGuia === undefined ? (
                      <></>
                    ) : (
                      <div className="nav-item text-start">
                        <a
                          className="nav-link"
                          href="#"
                          data-bs-toggle="collapse"
                          href="#volumetrico"
                          role="button"
                          aria-expanded={values?.pesoVol}
                          aria-controls="volumetrico"
                          onClick={activatePesoVol}
                        >
                          Agregar Peso Volumetrico
                        </a>
                      </div>
                    )}
                  </div>
                </div>
              </div>

              <div
                className="collapse w-100 align-items-start"
                id="volumetrico"
              >
                <div>
                  <div className="row ">
                    <div className="col-sm-12 col-md-4">
                      <div className="mb-3">
                        <label className="form-label-input">Largo</label>
                        <input
                          className="form-control"
                          type="number"
                          placeholder="0.00 Kg"
                          min={0}
                          name="largo"
                          onChange={handleChangeValue}
                          value={values?.largo}
                        />
                      </div>
                    </div>
                    <div className="col-sm-12 col-md-4">
                      <div className="mb-3">
                        <label className="form-label-input">Ancho</label>
                        <input
                          className="form-control"
                          type="number"
                          placeholder="0.00 Kg"
                          min={0}
                          name="ancho"
                          onChange={handleChangeValue}
                          value={values?.ancho}
                        />
                      </div>
                    </div>
                    <div className="col-sm-12 col-md-4">
                      <div className="mb-3">
                        <label className="form-label-input">Alto</label>
                        <input
                          className="form-control"
                          minLength={0}
                          debounceTimeout={500}
                          type="number"
                          placeholder="0.00 Kg"
                          name="alto"
                          min={0}
                          onChange={handleChangeValue}
                          value={values?.alto}
                        />
                      </div>
                    </div>
                  </div>
                  <div className="row mt-2">
                    <div className="col-sm-12 col-md-12">
                      <button
                        type="button"
                        className="btn-solid-secondary w-100 p-2 mb-1"
                        onClick={agregarAListaActualizados}
                      >
                        Agregar Peso (Peso Volumetrico)
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            </form>
          )}
        </div>

        <div className="card card-body">
          <h5 className="titleSection-form mt-2">Guias con Peso Actualizado</h5>
          <hr />
          <div className="row align-items-start">
            <div className="row table-cm">
              <table className="table dt-responsive nowrap">
                <thead>
                  <tr className="header-table">
                    <th
                      className="overflow-text"
                      data-bs-toggle="tooltip"
                      data-bs-placement="bottom"
                      title="Num. Guia"
                    >
                      ID
                    </th>
                    <th>Origen</th>
                    <th>Destino</th>
                    <th>Peso</th>
                    <th
                      className="overflow-text"
                      data-bs-toggle="tooltip"
                      data-bs-placement="bottom"
                      title="Ver Detalles"
                    >
                      Editar
                    </th>
                    <th
                      className="overflow-text"
                      data-bs-toggle="tooltip"
                      data-bs-placement="bottom"
                      title="Ver Detalles"
                    >
                      Eliminar
                    </th>
                  </tr>
                </thead>

                <tbody>
                  {ListGuiasPesoActualizado?.map((guia) => (
                    <tr
                      key={`actualizados_${guia.idGuia}`}
                      className="hover-table"
                    >
                      <td
                        className="overflow-text"
                        data-bs-toggle="tooltip"
                        data-bs-placement="bottom"
                        title={guia.idGuia}
                      >
                        {guia.idGuia}
                      </td>
                      <td
                        className="overflow-text"
                        data-bs-toggle="tooltip"
                        data-bs-placement="bottom"
                        title={`${guia.remitente.provincia}-${guia.remitente.canton}`}
                      >
                        {`${guia.remitente.provincia}-${guia.remitente.canton}`}
                      </td>
                      <td
                        className="overflow-text"
                        data-bs-toggle="tooltip"
                        data-bs-placement="bottom"
                        title={`${guia.destinatario.provincia}-${guia.destinatario.canton}`}
                      >
                        {`${guia.destinatario.provincia}-${guia.destinatario.canton}`}
                      </td>
                      <td
                        className="overflow-text"
                        data-bs-toggle="tooltip"
                        data-bs-placement="bottom"
                        title={`${guia.peso.pesototal}`}
                      >
                        {guia.peso.pesototal} kg
                      </td>
                      <td>
                        <div
                          type="button"
                          className="w-100 "
                          onClickCapture={() => editGuiaPeso(guia)}
                          data-bs-toggle="tooltip"
                          data-bs-placement="bottom"
                          title="Ver Detalles"
                        >
                          <i className="material-icons">mode_edit</i>
                        </div>
                      </td>
                      <td>
                        <div
                          type="button"
                          className="w-100 "
                          onClickCapture={() =>
                            eliminardeListaActualizados(guia)
                          }
                          data-bs-toggle="tooltip"
                          data-bs-placement="bottom"
                          title="Eliminar de la lista"
                        >
                          <i className="material-icons">delete_forever</i>
                        </div>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
            <div className="row">
              {ListGuiasPesoActualizado.length === 0 ? (
                <p className="text-muted">No hay guías en la lista</p>
              ) : (
                <></>
              )}
              {ListGuiasPesoActualizado.length >= 1 ? (
                <button
                  type="button"
                  className=" btn-dt-details col mx-1 bt-a p-2"
                  onClick={updateGuiasFirebase}
                >
                  Guardar Datos
                </button>
              ) : (
                <p className="text-muted"></p>
              )}
              {/* <button type="button" className="btn-dt col mx-1">Elimina</button>
                    <button type="button" className="btn btn-success col mx-1">Imprimir</button> */}
            </div>
          </div>
        </div>

        <Dashfooter />
      </div>
    </div>
  );
}

export default ActualizarPesos;
