import React from "react";
import "../asignacion_correspondencia/asignacion_correspondencia.css";

function RegistroCorrespondencia() {
  return (
    <div className="crearGuias col-md-9 ms-sm-auto col-lg-10 px-md-4">
      <div className="container-fluid pt-4">
        <h4 className="title-separtor">Registro de Corespondencia</h4>
        <hr />
        <div className="card card-body">
          <div className="row mb-4">
            <div className="col-sm-12 col-md-6">
              <form>
                <div className="input-group">
                  <label htmlFor="simpleinput" className="form-label-input">
                    Buscar Guia
                  </label>
                  <input
                    type="text"
                    className="form-control dropdown-toggle"
                    placeholder="Num. de Guia..."
                    id="top-search"
                  />
                  <span className="mdi mdi-magnify search-icon" />
                  <button className="btn-solid-secondary px-4 " type="submit">
                    Buscar
                  </button>
                </div>
              </form>
            </div>
          </div>

          <div className="col-sm-12 col-md-8">
            <h5 className="titleSection-form">Recibido</h5>
          </div>
          <hr />
          <div className="row ">
            <div className="col-sm-12 col-md-6">
              <div className="mb-3">
                <label   className="form-label-input">
                  Recibido por
                </label>
                <input
                  type="text"

                  className="form-control"
                  placeholder="Nombres/Apellidos"
                />
              </div>
            </div>
            <div className="col-sm-12 col-md-6">
              <div className="input-group">
                <label htmlFor="simpleinput" className="form-label-input">
                  Num. Cédula/DNI
                </label>
                <input
                  type="text"

                  className="form-control"
                  placeholder="Num. ruc/dni..."
                />
              </div>
            </div>
          </div>
          <div className="row ">
            <div className="col-sm-12 col-md-6 mb-3">
              <label htmlFor="simpleinput" className="form-label-input">
                Fecha
              </label>
              <input
                className="form-control"
                id="example-date"
                type="date"
                name="date"
              />
            </div>

            <div className="col-sm-12 col-md-6 mb-3">
              <label htmlFor="simpleinput" className="form-label-input">
                Hora
              </label>
              <input
                className="form-control"
                id="example-time"
                type="time"
                name="time"
              />
            </div>
          </div>
          <div className="row ">
            <div className="col-sm-12 col-md-6">
              <div className="mb-3">
                <label   className="form-label-input">
                  Novedad
                </label>
                <select
                  className="form-select overflow-auto"

                >
                  <option>NADIE EN CASA</option>
                  <option>CAMBIO DE DIRECCIÓN</option>
                  <option>DIRECCIÓN INCORRECTA</option>
                  <option>RECHAZADO</option>
                  <option>DESCONOCIDO</option>
                  <option>VUELTA A GESTIÓN</option>
                  <option>FUERZA MAYOR</option>
                </select>
              </div>
            </div>
            <div className="col-sm-12 col-md-6 mb-3">
              <label htmlFor="simpleinput" className="form-label-input">
                Descripcion Novedad
              </label>
              <input
                className="form-control"
                  
                type="text"
                placeholder="Descripsion de novedad"
                name="descripcion"
              />
            </div>
          </div>
        </div>

        <div className="card card-body">
          <div className="row align-items-center">
            <div className="col-sm-12 col-md-8">
              <h5 className="titleSection-form">Lista de Guias</h5>
            </div>
          </div>
          <hr />
          <div className="row align-items-start">
            <div className="row table-cm">
              <table className="table dt-responsive nowrap">
                <thead>
                  <tr className="header-table">
                    <th>ID</th>
                    <th>Remitente</th>
                    <th>Destinatario</th>
                    <th>Fecha</th>
                    <th>Ciudad</th>
                    <th>Cantón</th>
                    <th>Estado</th>
                    <th></th>
                    <th></th>
                  </tr>
                </thead>

                <tbody>
                  <tr className="hover-table">
                    <td>RFXDFSDSDDSDSDSS</td>
                    <td>Tiger Nixon</td>
                    <td>System Architect</td>
                    <td>18/12/20 15H30</td>
                    <td>Loja</td>
                    <td>Loja</td>
                    <td>Estado</td>
                    <td>
                      <button type="button" className=" btn-dt-limpiar w-100">
                        Editar
                      </button>
                    </td>
                    <td>
                      <button type="button" className=" btn-dt-del w-100">
                        Eliminar
                      </button>
                    </td>
                  </tr>
                  <tr className="hover-table">
                    <td>RFXDFSDSDDSDSDSS</td>
                    <td>Tiger Nixon</td>
                    <td>System Architect</td>
                    <td>18/12/20 15H30</td>
                    <td>Loja</td>
                    <td>Loja</td>
                    <td>Estado</td>
                    <td>
                      <button type="button" className=" btn-dt-limpiar w-100">
                        Editar
                      </button>
                    </td>
                    <td>
                      <button type="button" className=" btn-dt-del w-100">
                        Eliminar
                      </button>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
        <div className="card card-body">
          <div className="row">
            <button type="button" className="btn-dt col mx-1">
              Cancelar
            </button>
            <button type="button" className="btn btn-success col mx-1">
              Guardar e Imprimir
            </button>
          </div>
        </div>

        {/* <div className="card card-body">
					<div className="row">
						<button type="button" className=" btn-dt-del col mx-1 p-2 bt-a">
							Cancelar
						</button>
						<button type="button" className=" btn-dt-details col mx-1 bt-a">
							Anular
						</button> */}
        {/* <button type="button" className="btn-dt col mx-1">Elimina</button>
                    <button type="button" className="btn btn-success col mx-1">Imprimir</button> */}
        {/* </div>
				</div> */}
      </div>
    </div>
  );
}

export default RegistroCorrespondencia;
