import React from "react";
import { localidadesList } from "../../../../constants/db_provincias";
import { ParseDate } from "../../../../helpers/helper";
import "../crear-manifiestos/manifiestos.css";
import { v1 as uuid } from "uuid";
import CrearManifiestoHook from "../../../../hooks/crear_manifiestos_hook";
import { DebounceInput } from "react-debounce-input";
import Spinner from "../../../spinner/spinner";

function crearManifiesto() {
  let initialValues = {
    idManifiesto: uuid(),
    fechaCreacionManifiesto: Date.now(),
    destino: "",
  };

  const [
    fetching,
    values,
    searchText,
    showErrorDestino,
    listGuiasManifiesto,
    provinciaDestino,
    enviarManifiestos,
    deleteguiaFromManifiesto,
    handlerChange,
    searchGuias,
    handlerSearchText,
  ] = CrearManifiestoHook(initialValues);

  return (
    <div className="crearGuias col-md-9 ms-sm-auto col-lg-10 px-md-4">
      <div className="container-fluid pt-4">
        <h4 className="title-separtor">Crear Manifiestos</h4>
        <hr />
        <div className="card card-body">
          <div className="row ">
            <div className="col-lg-6 ">
              <form>
                <div className="input-group">
                  <label for="simpleinput" className="form-label-input">
                    Fecha
                  </label>
                  <input
                    type="text"
                    value={ParseDate(values?.fechaCreacionManifiesto)}
                    name="fechaCreacionManifiesto"
                    className="form-control"
                    placeholder={ParseDate(Date.now())}
                    readOnly
                  />
                </div>
              </form>
            </div>

            <div className="col-lg-6 ">
              <form>
                <div className="input-group">
                  <label for="simpleinput" className="form-label-input">
                    Num. Despacho
                  </label>
                  <input
                    type="text"
                    name="idManifiesto"
                    className="form-control"
                    value={values?.idManifiesto}
                    placeholder={uuid()}
                    readOnly
                  />
                </div>
              </form>
            </div>
          </div>
          <div className="row mt-4">
            <div className="col-lg-6 ">
              <form>
                <div className="mb-3">
                  <label className="form-label-input">Destino</label>
                  {listGuiasManifiesto.length === 0 ? (
                    <select
                      className="form-select"
                      id="example-select"
                      value={provinciaDestino}
                      onChange={handlerChange}
                      name="destino"
                      required
                    >
                      <option value="">Seleccione un Destino</option>
                      {localidadesList?.map((e) => {
                        return (
                          <option
                            key={`destino_${e.provincia}`}
                            value={e.provincia}
                          >
                            {e.provincia}
                          </option>
                        );
                      })}
                    </select>
                  ) : (
                    <input
                      type="text"
                      name="destino"
                      className="form-control"
                      value={provinciaDestino}
                      readOnly
                    />
                  )}
                </div>

                {showErrorDestino ? (
                  <p className="error">Seleccione un destino</p>
                ) : (
                  <> </>
                )}
              </form>
            </div>
            <div className="col-lg-6 ">
              <form>
                <div className="input-group">
                  <label for="simpleinput" className="form-label-input">
                    Buscar Guias
                  </label>
                  <DebounceInput
                    minLength={0}
                    debounceTimeout={300}
                    type="text"
                    className="form-control dropdown-toggle"
                    placeholder="Num. Guia..."
                    onChange={handlerSearchText}
                    value={searchText}
                    name="searchText"
                  />

                  {fetching ? (
                    <div className="container-spinner-buscar">
                      <Spinner />
                    </div>
                  ) : (
                    <button
                      className="btn-solid-secondary px-4 "
                      type="submit"
                      onClick={searchGuias}
                    >
                      Buscar
                    </button>
                  )}
                </div>
              </form>
            </div>
          </div>
        </div>
        <div className="card card-body">
          <div className="row align-items-center">
            <div className="col-sm-12 col-md-8">
              <h5 className="titleSection-form">Lista de Guias</h5>
            </div>
          </div>
          <hr />
          <div className="row align-items-start">
            <div className="row table-cm">
              <table className="table dt-responsive nowrap">
                <thead>
                  <tr className="header-table">
                    <th>ID</th>
                    <th>Fecha</th>
                    <th>Origen</th>
                    <th>Destino</th>
                    <th>Estado</th>
                    <th>Eliminar</th>
                    {/* <th /> */}
                  </tr>
                </thead>

                <tbody>
                  {listGuiasManifiesto?.map((guia) => {
                    return (
                      <tr key={`origen_${guia.idGuia}`} className="hover-table">
                        <td>{guia.idGuia}</td>
                        <td>{ParseDate(guia.fechaActual)}</td>
                        <td>
                          {`${guia.remitente.provincia}-${guia.remitente.canton}`}
                        </td>
                        <td>
                          {`${guia.destinatario.provincia}-${guia.destinatario.canton}`}
                        </td>
                        <td>{guia.estado}</td>
                        <td>
                          <div
                            type="button"
                            className=" "
                            onClick={() =>
                              deleteguiaFromManifiesto(guia.idGuia)
                            }
                          >
                            <i className="large material-icons">delete_forever</i>
                          </div>
                        </td>
                      </tr>
                    );
                  })}
                </tbody>
              </table>
              {listGuiasManifiesto.length === 0 ? (
                <div className="no-Item">
                  <p className="text-muted noGuias">No hay Guias Registradas</p>
                </div>
              ) : (
                <></>
              )}
            </div>
          </div>
        </div>
        <div className="card card-body">
          <div className="row">
            {listGuiasManifiesto.length === 0 ? (
              <div
                className="btn btn-success-disable col mx-1"
                // type="button"
              >
                Generar Manifiesto
              </div>
            ) : fetching ? (
              <div className="container-spinner-buscar col mx-1 w-100">
                <Spinner />
              </div>
            ) : (
              <button
                className="btn btn-success col mx-1"
                type="button"
                onClick={enviarManifiestos}
              >
                Generar Manifiesto
              </button>
            )}
          </div>
        </div>
      </div>
    </div>
  );
}

export default crearManifiesto;
