import { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { Switch, Route, Redirect } from "react-router-dom";
import AsignacionAcciones from "../component-dashboard/dash-board/gestion-plataforma/asignacion_acciones/asignacion-acciones";
import BuscarUsuarios from "../component-dashboard/dash-board/gestion_usuarios/buscar_usuario/buscar_usuarios";
import CrearUsuarios from "../component-dashboard/dash-board/gestion_usuarios/crear_usuarios/crear_usuarios";
import ActualizarPesos from "../component-dashboard/dash-board/guias/actualizar_pesos/ActualizarPesos";
import AnularGuias from "../component-dashboard/dash-board/guias/anular-guias/AnularGuias";
import BuscarGuias from "../component-dashboard/dash-board/guias/buscar_guias/buscar_guias";
import CrearGuias from "../component-dashboard/dash-board/guias/crear-guias/CrearGuias";
import AsignacionCorrespondencia from "../component-dashboard/dash-board/manifiestos/asignacion_correspondencia/asignacion_correspondencia";
import crearManifiesto from "../component-dashboard/dash-board/manifiestos/crear-manifiestos/manifiestos";
import RecepcionManifiestos from "../component-dashboard/dash-board/manifiestos/recepcion_manifiestos/recepcion_manifiestos";
import RegistroCorrespondencia from "../component-dashboard/dash-board/manifiestos/registro_correspondencia/registro_correspondencia";
import Slidebar from "../component-dashboard/dash-board/slide-bar/slide-bar";
import DashHome from "../component-dashboard/dash-componets/dashboard_home/DashHome";
import EstructuraGuia from "../component-dashboard/estructura-guia-imprimir/estructura-print";
import { PublicRoute } from "./PublicRoute";

export const DashboardRoute = () => {
  const {
    auth: { funciones },
  } = useSelector((state) => state);

  return (
    <>
      <div className="row">
        {/* <Switch> */}

        <Slidebar />
        <Switch>
          <PublicRoute
            isAutenticated={!funciones?.g_crear}
            path={"/dashboard/crearGuia"}
            component={CrearGuias}
          />

          <PublicRoute
            isAutenticated={!funciones?.g_anular}
            path={"/dashboard/anularGuia"}
            component={AnularGuias}
          />

          <PublicRoute
            isAutenticated={!funciones?.g_buscar}
            path={"/dashboard/buscarGuias"}
            component={BuscarGuias}
          />

          <PublicRoute
            isAutenticated={!funciones?.g_actualizarPeso}
            path={"/dashboard/actualizarPesos"}
            component={ActualizarPesos}
          />

          <PublicRoute
            isAutenticated={!funciones?.m_crear}
            path={"/dashboard/manifiestos"}
            component={crearManifiesto}
          />

          <PublicRoute
            isAutenticated={!funciones?.u_buscar}
            path={"/dashboard/buscarusuarios"}
            component={BuscarUsuarios}
          />

          <PublicRoute
            isAutenticated={!funciones?.u_crear}
            path={"/dashboard/crearUsuarios"}
            component={CrearUsuarios}
          />

          <PublicRoute
            isAutenticated={!funciones?.u_crear}
            path={"/dashboard/RecepcionManifiestos"}
            component={RecepcionManifiestos}
          />

          <PublicRoute
            isAutenticated={!funciones?.m_asignar}
            path={"/dashboard/AsignacionCorrespondencia"}
            component={AsignacionCorrespondencia}
          />

          <PublicRoute
            isAutenticated={!funciones?.m_registrar}
            path={"/dashboard/registroCorrespondencia"}
            component={RegistroCorrespondencia}
          />

          <PublicRoute
            isAutenticated={!funciones?.p_asignacion}
            path={"/dashboard/AsignacionAcciones"}
            component={AsignacionAcciones}
          />
          {/* <PublicRoute
            isAutenticated={!funciones?.g_crear}
            path={"/dashboard/formato"}
            component={EstructuraGuia}  
          /> */}
          <PublicRoute
            isAutenticated={!funciones?.g_crear}
            path={"/dashboard"}
            component={DashHome}
          />
        </Switch>
      </div>
      {/* </div> */}
    </>
  );
};
