import React, { useState } from 'react';
import '../contactanos/contactanos.css';
import Modal from "react-modal";
import clienteAxios from '../../config/axios';
import { faCheckCircle } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';


Modal.setAppElement("#root");
const customStyles = {
    content: {
        top: "50%",
        left: "50%",
        right: "auto",
        bottom: "auto",
        marginRight: "-50%",
        transform: "translate(-50%, -50%)",
        width: "25%",
    },
};


function Contactanos() {
    const [nombre, setNombre] = useState('');
    const [correo, setCorreo] = useState('');
    const [telefono, setTelefono] = useState('');
    const [mensaje, setMensaje] = useState('');
    const [error, setError] = useState('');
    const [modalIsOpen, setIsOpen] = useState(false);

    const openModal = () => {
        setIsOpen(true);
    };

    const closeModal = () => {
        setIsOpen(false);
    };

    const sendEmail = async () => {
        // console.log("ENVIANDO CORREO...");
        try {
            await clienteAxios.post('/api/mail/sendemail', {
                nombre,
                correo,
                telefono,
                mensaje,
            });
            // console.log(resp);
            setError('');
            // Mostrar Mensaje de ok
            openModal();
            setCorreo('');
            setNombre('');
            setMensaje('');
            setTelefono('');
        } catch (error) {
            // console.log("Error en SendEmail");
            // console.log(error);
            setError("No se pudo Enviar el Correo. Intentalo más tarde.");
        }
    }

    const verifyForm = () => {
        const emailRegex = /^[-\w.%+]{1,64}@(?:[A-Z0-9-]{1,63}\.){1,125}[A-Z]{2,63}$/i;
        // console.log(emailRegex.test(correo));
        const isEmail = emailRegex.test(correo);

        if (!isEmail) {
            setError('El correo no es válido.')
        }
        if (nombre === '') {
            setError('El nombre es obligatorio.')
        }
        if (telefono === '') {
            setError('El teléfono es obligatorio.')
        }
        if (mensaje === '') {
            setError('El mensaje es obligatorio.')
        }
        if (nombre.trim() !== '' && correo !== '' && isEmail && telefono.trim() !== '' && mensaje.trim() !== '') {
            // console.log(isEmail);
            sendEmail();
        }
    }
    return (
        <div>
            <section className="page-section" id="contact">
                <div className="container">
                    <div className="text-center">
                        <h2 className="section-heading text-uppercase">Reclamos o Sugerencias</h2>
                        <h3 className="subheading">Déjanos tu mensaje</h3>
                    </div>
                    <form id="contactForm">
                        <div className="row align-items-stretch mb-5">
                            <div className="col-md-6">
                                <div className="form-group">
                                    <input className="form-control" id="name" type="text" value={nombre} onChange={(e) => setNombre(e.target.value)} placeholder="Nombre *" required="required" />
                                    <p className="help-block text-danger"></p>
                                </div>
                                <div className="form-group">
                                    <input className="form-control" id="email" type="email" value={correo} onChange={(e) => setCorreo(e.target.value)} placeholder="Correo *" required="required" />
                                    <p className="help-block text-danger"></p>
                                </div>
                                <div className="form-group mb-md-0">
                                    <input className="form-control" id="phone" type="tel" value={telefono} onChange={(e) => setTelefono(e.target.value)} placeholder="Teléfono *" required="required" />
                                    <p className="help-block text-danger"></p>
                                </div>
                            </div>
                            <div className="col-md-6">
                                <div className="form-group form-group-textarea mb-md-0">
                                    <textarea className="form-control" id="message" placeholder="Tu mensaje *" value={mensaje} onChange={(e) => setMensaje(e.target.value)} required="required"></textarea>
                                    <p className="help-block text-danger"></p>
                                </div>
                            </div>
                        </div>
                        {
                            error !== '' ?
                                <p className="error">{error}</p>
                                : <></>
                        }
                        <div className="text-center">
                            <div className="btn-line" onClickCapture={() => verifyForm()}>
                                Enviar
                            </div>
                        </div>
                    </form>
                    <Modal
                        isOpen={modalIsOpen}
                        onRequestClose={closeModal}
                        style={customStyles}
                        contentLabel="Mensaje"
                    >
                        <div className="container">
                            <div className="w-100 d-flex justify-content-center align-items-center">
                                <FontAwesomeIcon icon={faCheckCircle} className="h2 text-success text-center" />
                            </div>
                            <h5 className="text-success">Correo Envíado! Espera una respuesta</h5>

                        </div>
                    </Modal>
                </div>
            </section>
        </div>
    )
}

export default Contactanos
