import {
  LOGIN,
  LOGIN_SUCCESS,
  LOGIN_ERROR,
  REGISTER_USER,
  REGISTER_USER_ERROR,
  REGISTER_USER_SUCCESS,
  CERRAR_SESION,
  GET_ROL_FUNCTIONS,
  GET_ROL_FUNCTIONS_ERROR,
  GET_ROL_FUNCTIONS_SUCCESS,
} from "../../types/types";
import { auth, db } from "../../firebase";

export const userFunctions = async (dispatch, idrol) => {
  dispatch({
    type: GET_ROL_FUNCTIONS,
  });
  try {
    const resp = await db.collection("roles").doc(idrol).get();
    const temp = resp.data();
    dispatch({
      type: GET_ROL_FUNCTIONS_SUCCESS,
      payload: temp.funciones,
    });
    localStorage.setItem("rfas_funciones", JSON.stringify(temp.funciones));
  } catch (error) {
    console.log("===> Error al obtener Funciones ");
    console.log(error);
    dispatch({
      type: GET_ROL_FUNCTIONS_ERROR,
      payload: "Error al buscar funciones",
    });
  }
};

export const startLogin = (email, password, reset, msgError, history) => {
  return (dispatch) => {
    dispatch({
      type: LOGIN,
    });
    try {
      auth
        .signInWithEmailAndPassword(email, password)
        .then(async ({ user }) => {
          // console.log("====> usuario logeado")
          const accessToken = await user.getIdToken();
          console.log("===> USUARIO LOGEADO");
          console.log(user);

          const userTemp = await db
            .collection("usuarios")
            .where("email", "==", email)
            .get();
          let data;
          userTemp.docs.forEach((element) => {
            data = element.data();
            data.id = element.id;
          });
          console.log("===> USUARIO DATA");
          console.log(data);
          console.log("ANTES DE DISPATCH LOGIN")
          await userFunctions(dispatch, data.idrol);
          dispatch({
            type: LOGIN_SUCCESS,
            payload: {
              accessToken,
              dataProfile: data,
            },
          });
          console.log("DESPUES DE DISPATCH LOGIN")
          // Guardar en LocalStorage
          localStorage.setItem("accessToken", accessToken);
          localStorage.setItem("dataProfile", JSON.stringify(data));
          console.log("DESPUES DE LOCALSTORAGE")
          reset();
          history.replace("/dashboard");
        })
        .catch((error) => {
          console.log("=====> Error en Login");
          console.log(error);
          dispatch({
            type: LOGIN_ERROR,
            payload: "Error al logearse",
          });
          msgError("Error al Iniciar sesión");
        });
    } catch (error) {
      // console.log("=====> Error en Login CATCH");
      // console.log(error);
      dispatch({
        type: LOGIN_ERROR,
        payload: "Error al logearse",
      });
    }
  };
};
export const cerrarSesion = (history) => {
  return async (dispatch) => {
    try {
      await auth.signOut();
      localStorage.removeItem("accessToken");
      localStorage.removeItem("dataProfile");
      localStorage.removeItem("rfas_funciones");
      dispatch({
        type: CERRAR_SESION,
      });
      history.replace("/login");
    } catch (error) {
      console.log("===> Error al cerrar sesión");
      console.log(error);
    }
  };
};
export const createUser = (dataUser, password, reset, msgOk, msgError) => {
  return async (dispatch) => {
    dispatch({
      type: REGISTER_USER,
    });
    try {
      // console.log(dataUser.email);
      // console.log(password);
      const resp = await auth
        .createUserWithEmailAndPassword(dataUser.email, password)
        .then(async ({ user }) => {
          // console.log("======> USUARIO CREADO");
          return user;
        })
        .catch((error) => {
          console.log("======> Error al crear usuario");
          console.log(error);
          msgError();
          dispatch({
            type: REGISTER_USER_ERROR,
            payload: "Error al crear usuario",
          });
          return null;
        });
      console.log("===> USUARIO DESDE FIREBASE");
      console.log(resp);
      if (resp !== null) {
        // GUARDAR USUARIO EN BD
        const rolResp = await db
          .collection("roles")
          .where("rol", "==", dataUser.rol)
          .get();
        let idrol;
        rolResp.docs.forEach((temp) => {
          idrol = temp.id;
        });
        dataUser.idrol = idrol;
        await db.collection("usuarios").add(dataUser);
        msgOk();
        reset();
        dispatch({
          type: REGISTER_USER_SUCCESS,
        });
      }
    } catch (error) {
      console.log("==> ERROR REGISTER");
      console.log(error);
      msgError();
      dispatch({
        type: REGISTER_USER_ERROR,
        payload: "Error al crear usuario",
      });
    }
  };
};
