import "../buscar_guias/buscar_guias.css";
import React, { useState } from "react";
import { useSelector } from "react-redux";

import { ParseDate } from "../../../../helpers/helper";
import Spinner from "../../../spinner/spinner";
import Dashfooter from "../../../dash-componets/footter/dashfooter";
import { UseFormDardeBajaGuias } from "../../../../hooks/user_from_buscar_guias_dardebaja";
import { listTextGuias } from "../../../../constants/constants";
import EstructuraGuia from "../../../estructura-guia-imprimir/estructura-print";

function BuscarGuias() {
  const [
    values,
    fetching,
    listGuias,
    CurrentGuia,
    darDeBaja,
    solicitarAnulacion,
    handlerChangeText,
    getGuiaSearch,
    showDetails,
    dowloadGuia,
    printGuiaAction,
  ] = UseFormDardeBajaGuias();

  const {
    remitente,
    destinatario,
    peso,
    contenido,
    //TODo mostrar fecha de guia creada
    // fechaActual,
    // idGuia,
    numPiezas,
    tipo,
  } = CurrentGuia;

  return (
    <div className="crearGuias col-md-9 ms-sm-auto col-lg-10 px-md-4">
      <div className="container-fluid pt-4 d-print-none ">
        <h4 className="title-separtor">Rastrear Guía</h4>
        <hr />
        <div className="card card-body">
          <div className="row align-items-center mb-4">
            <div className="col-sm-12 col-md-6">
              <label htmlFor="simpleinput" className="form-label-input">
                Buscar Por
              </label>
              <select
                className="form-select"
                value={values?.busqueda}
                onChange={handlerChangeText}
                name="busqueda"
              >
                <option value="guia">Número de Guía</option>
                <option value="cedula_destinatario">
                  Cédula / RUC Destinatario
                </option>
                <option value="cedula_remitente">Cédula / RUC Remitente</option>
              </select>
            </div>

            <div className="col-sm-12 col-md-6">
              <form>
                <label htmlFor="simpleinput" className="form-label-input">
                  Buscar
                </label>
                <div className="input-group">
                  <input
                    type="text"
                    className="form-control dropdown-toggle"
                    placeholder="Buscar ..."
                    id="top-search"
                    name="textSearch"
                    value={values?.textSearch}
                    onChange={handlerChangeText}
                  />
                  <span className="mdi mdi-magnify search-icon" />
                  {fetching ? (
                    <div className="container-spinner-buscar">
                      <Spinner />
                    </div>
                  ) : (
                    <button
                      className="btn-solid-secondary px-4 "
                      type="button"
                      onClickCapture={getGuiaSearch}
                    >
                      Buscar
                    </button>
                  )}
                </div>
              </form>
            </div>
          </div>
        </div>
        <div className="card card-body">
          <div className="col-sm-12 col-md-6">
            <h5 className="titleSection-form">Listado de Guias</h5>
          </div>
          <hr />
          <div className="row align-items-start">
            <div className="row table-cm table-responsive">
              <table className="table  nowrap">
                <thead>
                  <tr className="header-table">
                    <th>ID</th>
                    <th
                      className="text-truncate"
                      data-toggle="tooltip"
                      data-placement="top"
                      title="Estado"
                    >
                      Estado
                    </th>
                    <th
                      className="text-truncate"
                      data-toggle="tooltip"
                      data-placement="top"
                      title="Fecha"
                    >
                      Fecha
                    </th>
                    <th
                      className="text-truncate"
                      data-toggle="tooltip"
                      data-placement="top"
                      title="Origen"
                    >
                      Origen
                    </th>
                    <th
                      className="text-truncate"
                      data-toggle="tooltip"
                      data-placement="top"
                      title="Destino"
                    >
                      Destino
                    </th>

                    <th
                      className="text-truncate"
                      data-toggle="tooltip"
                      data-placement="top"
                      title="Acciones"
                    >
                      Acciones
                    </th>
                  </tr>
                </thead>

                <tbody>
                  {/* {listTextGuias?.map((guia) => ( */}
                  {listGuias?.map((guia) => (
                    <tr key={guia?.idGuia} className="hover-table">
                      <td
                        className="text-truncate"
                        data-toggle="tooltip"
                        data-placement="top"
                        title={guia?.idGuia}
                      >
                        {guia?.idGuia}
                      </td>
                      <td
                        className="text-truncate"
                        data-toggle="tooltip"
                        data-placement="top"
                        title={
                          guia?.estado === null || guia?.estado === ""
                            ? "-----"
                            : guia?.estado
                        }
                      >
                        {guia?.estado === null || guia?.estado === ""
                          ? "-----"
                          : guia?.estado}
                      </td>
                      <td
                        className="text-truncate"
                        data-toggle="tooltip"
                        data-placement="top"
                        title={ParseDate(guia?.fechaActual)}
                      >
                        {ParseDate(guia?.fechaActual)}
                      </td>
                      <td
                        className="text-truncate"
                        data-toggle="tooltip"
                        data-placement="top"
                        title={`${guia?.remitente?.provincia}-${guia?.remitente?.canton}`}
                      >
                        {`${guia?.remitente?.provincia}-${guia?.remitente?.canton}`}
                      </td>
                      <td
                        className="text-truncate"
                        data-toggle="tooltip"
                        data-placement="top"
                        title={`${guia?.destinatario?.provincia}-${guia?.destinatario?.canton}`}
                      >
                        {`${guia?.destinatario?.provincia}-${guia?.destinatario?.canton}`}
                      </td>

                      <td className="text-truncate">
                        <a
                          className="actions view"
                          type="button"
                          data-toggle="tooltip"
                          data-placement="top"
                          title="Ver Detalles"
                          onClick={(e) => showDetails(true, guia, e)}
                        >
                          <i className="material-icons">remove_red_eye</i>
                        </a>
                        <a
                          className="actions file"
                          type="button"
                          data-toggle="tooltip"
                          data-placement="top"
                          title="Descargar"
                          onClick={() => dowloadGuia(guia)}
                        >
                          <i className="material-icons">archive</i>
                        </a>
                        <a
                          className="actions print"
                          type="button"
                          data-toggle="tooltip"
                          data-placement="top"
                          title="Imprimir"
                          onClick={() => printGuiaAction(guia)}
                        >
                          <i className="material-icons">local_printshop</i>
                        </a>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
              {listGuias.length === 0 ? (
                <div>
                  <p>No hay items en la lista</p>
                </div>
              ) : (
                <></>
              )}
            </div>
          </div>
          <hr />
        </div>

        <h4 className="title-separtor">Detalles</h4>
        <hr />

        {values?.shoDetail ? (
          <div className="form">
            <div className="card card-body">
              <div className="w-100 p-2 text-end">
                <button
                  type="button"
                  className="btn-close"
                  aria-label="Close"
                  onClick={() => showDetails(false, null)}
                ></button>
              </div>
              <div className="row ">
                <div className="col-lg-6">
                  <h5 className="titleSection-form">Remitente</h5>
                  <hr />
                  <div className="mb-3">
                    {/* <i className="material-icons">add</i> */}
                    <label className="form-label-input">Ruc/Dni</label>
                    <input
                      readOnly
                      type="text"
                      className="form-control"
                      placeholder="110XXXXXXXXX"
                      value={remitente?.ruc}
                      name="remitente ruc"
                    />
                  </div>
                  <div className="mb-3">
                    <label className="form-label-input">ID Remitente</label>
                    <input
                      readOnly
                      type="text"
                      className="form-control"
                      value={remitente?.idRemitente}
                      name="idRemitente"
                    />
                  </div>
                  <div className="mb-3">
                    <label className="form-label-input">
                      Nombres Completos
                    </label>
                    <input
                      readOnly
                      type="text"
                      className="form-control"
                      placeholder="Nombres Completos"
                      value={remitente?.nombres}
                      name="remitente nombres"
                      required
                    />
                  </div>
                  <div className="mb-3">
                    <label className="form-label-input">Teléfono</label>
                    <input
                      readOnly
                      type="text"
                      className="form-control"
                      placeholder="Teléfono Principal"
                      value={remitente?.telefono}
                      name="remitente telefono"
                      required
                    />
                  </div>
                  <div className="mb-3">
                    <label className="form-label-input">Provincia</label>
                    <input
                      readOnly
                      type="text"
                      className="form-control"
                      placeholder="Loja"
                      value={remitente?.provincia}
                      name="remitente provincia"
                      required
                    />
                  </div>
                  <div className="mb-3">
                    <label className="form-label-input">Cantón</label>
                    <input
                      readOnly
                      type="text"
                      className="form-control"
                      placeholder="Loja"
                      value={remitente?.canton}
                      name="remitente canton"
                    />
                  </div>
                  <div className="mb-3">
                    <label className="form-label-input">Parroquia</label>
                    <input
                      readOnly
                      type="text"
                      className="form-control"
                      placeholder="Loja"
                      value={remitente?.parroquias}
                      name="remitente canton"
                    />
                  </div>
                  <div className="mb-3">
                    <label className="form-label-input">Dirección</label>
                    <input
                      readOnly
                      type="text"
                      className="form-control"
                      placeholder="Praza Ramón, 31, 8º F"
                      value={remitente?.direccion}
                      name="remitente direccion"
                    />
                  </div>
                  <div className="mb-3">
                    <label className="form-label-input">Referencia</label>
                    <input
                      readOnly
                      type="text"
                      className="form-control"
                      placeholder="Praza Ramón, 31, 8º F"
                      value={remitente?.referencia}
                      name="remitente referencia"
                    />
                  </div>
                  <div className="mb-3">
                    <label className="form-label-input">Código Postal</label>
                    <input
                      readOnly
                      type="text"
                      className="form-control"
                      placeholder="Cod. postal"
                      name="remitente codigoPostal"
                      value={remitente?.codigoPostal}
                    />
                  </div>
                  <div className="mb-3">
                    <label className="form-label-input">Email</label>
                    <input
                      readOnly
                      type="email"
                      className="form-control"
                      placeholder="example@example.com"
                      name="remitente email"
                      value={remitente?.email}
                    />
                  </div>
                </div>
                <div className="col-lg-6 ">
                  <h5 className="titleSection-form">Destinatario</h5>
                  <hr />

                  <div className="mb-3">
                    <label className="form-label-input">Ruc/Dni</label>
                    <input
                      readOnly
                      type="text"
                      className="form-control"
                      placeholder="110XXXXXXXXX"
                      name="destinatario ruc"
                      value={destinatario?.ruc}
                    />
                  </div>
                  <div className="mb-3">
                    <label className="form-label-input">ID Destinatario</label>
                    <input
                      readOnly
                      type="text"
                      className="form-control"
                      value="RF45540000001"
                      value={destinatario?.idDestinatario}
                    />
                  </div>
                  <div className="mb-3">
                    <label className="form-label-input">
                      Nombres Completos
                    </label>
                    <input
                      readOnly
                      type="text"
                      className="form-control"
                      placeholder="Nombres Completos"
                      name="destinatario nombres"
                      value={destinatario?.nombres}
                    />
                  </div>
                  <div className="mb-3">
                    <label className="form-label-input">
                      Télefono Principal
                    </label>
                    <input
                      readOnly
                      type="text"
                      className="form-control"
                      placeholder="Apellidos Completos"
                      name="destinatario telefono1"
                      value={destinatario?.telefono1}
                    />
                  </div>
                  <div className="mb-3">
                    <label className="form-label-input">Télefono 1</label>
                    <input
                      readOnly
                      type="text"
                      className="form-control"
                      placeholder="Télefono 1"
                      name="destinatario telefono2"
                      value={destinatario?.telefono2}
                    />
                  </div>
                  <div className="mb-3">
                    <label className="form-label-input">Télefono 2</label>
                    <input
                      readOnly
                      type="text"
                      className="form-control"
                      placeholder="Télefono 2"
                      name="destinatario telefono3"
                      value={destinatario?.telefono3}
                    />
                  </div>
                  <div className="mb-3">
                    <label className="form-label-input">Provincia</label>
                    <input
                      readOnly
                      type="text"
                      className="form-control"
                      placeholder="Loja"
                      value={destinatario?.provincia}
                      name="remitente provincia"
                      required
                    />
                  </div>
                  <div className="mb-3">
                    <label className="form-label-input">Cantón</label>
                    <input
                      readOnly
                      type="text"
                      className="form-control"
                      placeholder="Loja"
                      value={destinatario?.canton}
                      name="remitente canton"
                    />
                  </div>
                  <div className="mb-3">
                    <label className="form-label-input">Parroquia</label>
                    <input
                      readOnly
                      type="text"
                      className="form-control"
                      placeholder="Loja"
                      value={destinatario?.parroquias}
                      name="remitente canton"
                    />
                  </div>
                  <div className="mb-3">
                    <label className="form-label-input">Dirección</label>
                    <input
                      readOnly
                      type="text"
                      className="form-control"
                      placeholder="Praza Ramón, 31, 8º F"
                      value={destinatario?.direccion}
                      name="destinatario direccion"
                      required
                    />
                  </div>
                  <div className="mb-3">
                    <label className="form-label-input">Referencia</label>
                    <input
                      readOnly
                      type="text"
                      className="form-control"
                      placeholder="Praza Ramón, 31, 8º F"
                      value={destinatario?.referencia}
                      name="destinatario referencia"
                    />
                  </div>
                  <div className="mb-3">
                    <label className="form-label-input">Código Postal</label>
                    <input
                      readOnly
                      type="text"
                      className="form-control"
                      placeholder="Cod. Postal"
                      name="destinatario codigoPostal"
                      value={destinatario?.codigoPostal}
                    />
                  </div>
                  <div className="mb-3">
                    <label className="form-label-input">Email</label>
                    <input
                      readOnly
                      type="email"
                      className="form-control"
                      placeholder="example@example.com"
                      name="destinatario email"
                      value={destinatario?.email}
                      required
                    />
                  </div>
                  {/* <div className="mb-3">
                        <button
                          type="button"
                          className="btn-line-info col  bt-a p-2 w-100"
                        >
                          Guardar Destinatario
                        </button>
                      </div> */}
                </div>
              </div>
              <div className="div">
                <h5 className="titleSection-form">Informacion Adicional</h5>
                <hr />
                <div className="row">
                  <div className="col-lg-6 ">
                    <div className="mb-3">
                      <label className="form-label-input">Tipo</label>
                      <input
                        readOnly
                        className="form-control"
                        type="text"
                        placeholder=""
                        min={0}
                        value={tipo}
                        name="tipo"
                      />
                    </div>
                    <div className="mb-3">
                      <label className="form-label-input">
                        Número de Pienzas
                      </label>
                      <input
                        readOnly
                        className="form-control"
                        type="number"
                        placeholder="1"
                        min={0}
                        value={numPiezas}
                        name="numPiezas"
                      />
                    </div>
                    <div className="mb-3">
                      <label className="form-label-input">Contenido</label>
                      <textarea
                        className="form-control"
                        rows="5"
                        placeholder="Describe el contenido del producto"
                        value={contenido}
                        name="contenido"
                        readOnly
                      />
                    </div>
                  </div>

                  <div className="col-lg-6">
                    <form>
                      <div className="mb-3">
                        <label className="form-label-input">Peso</label>
                        <input
                          readOnly
                          type="number"
                          className="form-control"
                          placeholder="0.00 Kg"
                          name="peso pesototal"
                          value={peso?.pesototal}
                        />
                      </div>
                      <div className="mb-3">
                        <label className="form-label-input">Largo</label>
                        <input
                          readOnly
                          className="form-control"
                          type="number"
                          name="number"
                          placeholder={0}
                          name="peso largo"
                          value={peso?.largo}
                        />
                      </div>
                      <div className="mb-3">
                        <label className="form-label-input">Ancho</label>
                        <input
                          readOnly
                          className="form-control"
                          type="number"
                          name="number"
                          placeholder={0}
                          name="peso ancho"
                          value={peso?.ancho}
                        />
                      </div>
                      <div className="mb-3">
                        <label className="form-label-input">Alto</label>
                        <input
                          readOnly
                          className="form-control"
                          type="number"
                          name="number"
                          placeholder={0}
                          name="peso alto"
                          value={peso?.alto}
                        />
                      </div>

                      <div className="mb-3">
                        <label className="form-label-input">
                          Valor Declarado
                        </label>
                        <input
                          readOnly
                          type="text"
                          className="form-control"
                          value="$ 0.00"
                        />
                      </div>
                      <div className="mb-3">
                        <label className="form-label-input">
                          Valor Asegurado
                        </label>
                        <input
                          readOnly
                          type="text"
                          className="form-control"
                          value="$ 0.00"
                        />
                      </div>
                    </form>
                  </div>
                </div>
                <hr />
                <div className="row py-2">
                  {/* {CurrentGuia?.estado === "de baja" ||
                  CurrentGuia?.estado === "pendiente anulacion" ? (
                    <div className="col-sm-12 col-md-4"></div>
                  ) : (
                    <div className="col-sm-12 col-md-4">
                      <div
                        className="nav-link"
                        data-bs-toggle="collapse"
                        href="#pesosvl"
                        role="button"
                        aria-expanded="false"
                        aria-controls="pesosvl"
                        type="button"
                        className="btn btn-primary w-100  mb-1"
                      >
                        Dar de Baja
                      </div>
                    </div>
                  )} */}
                  <div className="col-sm-12 col-md-4"></div>
                  <div className="col-sm-12 col-md-4">
                    <button
                      onClick={() => {
                        showDetails(false, null);
                      }}
                      type="button"
                      className="btn-dt-delete w-100 p-2 mb-1"
                    >
                      Cerrar
                    </button>
                  </div>

                  <div className="col-sm-12 col-md-4">
                    {CurrentGuia?.estado === "pendiente anulacion" ? (
                      <></>
                    ) : (
                      <button
                        type="button"
                        className=" w-100 btn btn-danger"
                        data-bs-toggle="modal"
                        data-bs-target="#anularguia"
                      >
                        Solicitar Anulación
                      </button>
                    )}

                    <div
                      className="modal fade"
                      id="anularguia"
                      data-bs-backdrop="static"
                      data-bs-keyboard="false"
                      aria-labelledby="staticBackdropLabel"
                      aria-hidden="true"
                    >
                      <div className="modal-dialog modal-dialog-centered">
                        <div className="modal-content">
                          <div className="modal-header">
                            <h5
                              className="modal-title"
                              id="staticBackdropLabel"
                            >
                              Solicitar Anulacion de guia
                            </h5>
                            <button
                              type="button"
                              className="btn-close"
                              data-bs-dismiss="modal"
                              aria-label="Close"
                            ></button>
                          </div>

                          <div className="modal-body">
                            <p>
                              Esta seguro de solicitar la anulacion de esta
                              guia, una ves solicitada no se podra cambiar este
                              estado.
                            </p>
                            <input
                              type="text"
                              placeholder="ingrese el motivo de anulacion"
                              className="form-control"
                              onChange={handlerChangeText}
                              value={values?.motivoAnulacion}
                              name="motivoAnulacion"
                              required
                            />
                          </div>

                          <div className="modal-footer">
                            <button
                              type="button"
                              className="btn btn-secondary"
                              data-bs-dismiss="modal"
                              onClick={() => {
                                showDetails(false, null);
                              }}
                            >
                              Cancelar
                            </button>
                            <button
                              type="submit"
                              className=" btn btn-danger p-2"
                              data-bs-dismiss="modal"
                              onClick={solicitarAnulacion}
                            >
                              Enviar Solicitud
                            </button>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>

            <div className="card card-body collapse" id="pesosvl">
              <form>
                <div className="list-group-flush">
                  <div className="col-sm-12 col-md-8">
                    <h5 className="titleSection-form">Dar de Baja</h5>
                  </div>
                  <hr />
                  <div className="row ">
                    <div className="col-sm-12 col-md-6">
                      <div className="mb-3">
                        <label className="form-label-input">Recibido por</label>
                        <input
                          onChange={handlerChangeText}
                          type="text"
                          className="form-control"
                          placeholder="Nombres/Apellidos"
                          name="recibido"
                          required
                        />
                      </div>
                    </div>
                    <div className="col-sm-12 col-md-6">
                      <div className="input-group">
                        <label
                          htmlFor="simpleinput"
                          className="form-label-input"
                        >
                          Num. Cédula/DNI
                        </label>
                        <input
                          onChange={handlerChangeText}
                          type="text"
                          className="form-control"
                          placeholder="Num. ruc/dni..."
                          name="numCedula"
                          required
                        />
                      </div>
                    </div>
                  </div>
                  <div className="row ">
                    <div className="col-sm-12 col-md-6 mb-3">
                      <label htmlFor="simpleinput" className="form-label-input">
                        Fecha
                      </label>
                      <input
                        onChange={handlerChangeText}
                        className="form-control"
                        id="example-date"
                        type="date"
                        name="fechaEntregado"
                        required
                      />
                    </div>

                    <div className="col-sm-12 col-md-6 mb-3">
                      <label htmlFor="simpleinput" className="form-label-input">
                        Hora
                      </label>
                      <input
                        onChange={handlerChangeText}
                        className="form-control"
                        id="example-time"
                        type="time"
                        name="horaEntregado"
                        required
                      />
                    </div>
                  </div>
                  <div className="row ">
                    <div className="col-sm-12 col-md-6 mb-3">
                      <label htmlFor="simpleinput" className="form-label-input">
                        Parentesco
                      </label>
                      <input
                        onChange={handlerChangeText}
                        type="text"
                        className="form-control"
                        placeholder="Padre, Hijo, Esposo"
                        name="parentesco"
                        required
                      />
                    </div>
                    <div className="col-sm-12 col-md-6 mb-3">
                      <label htmlFor="simpleinput" className="form-label-input">
                        Novedad
                      </label>
                      <input
                        onChange={handlerChangeText}
                        type="text"
                        className="form-control"
                        placeholder="Escribe un comentario"
                        name="novedad"
                      />
                    </div>
                  </div>
                </div>
              </form>
              <div className="row">
                <div className="col-sm-12 col-md-6"></div>
                <div className="col-sm-12 col-md-6">
                  {/* <!-- Button trigger modal --> */}
                  <button
                    type="button"
                    className=" w-100 btn btn-primary"
                    data-bs-toggle="modal"
                    data-bs-target="#staticBackdrop"
                  >
                    Dar de Baja
                  </button>

                  {/* <!-- Modal --> */}
                  <div
                    className="modal fade"
                    id="staticBackdrop"
                    data-bs-backdrop="static"
                    data-bs-keyboard="false"
                    aria-labelledby="staticBackdropLabel"
                    aria-hidden="true"
                  >
                    <div className="modal-dialog modal-dialog-centered">
                      <div className="modal-content">
                        <div className="modal-header">
                          <h5 className="modal-title" id="staticBackdropLabel">
                            Dar de Baja?
                          </h5>
                          <button
                            type="button"
                            className="btn-close"
                            data-bs-dismiss="modal"
                            aria-label="Close"
                          ></button>
                        </div>
                        <div className="modal-body">
                          <p>
                            Al actualizar el estado de la guia ya no podras
                            cambiarlo al estado actual.
                          </p>
                        </div>
                        <div className="modal-footer">
                          <button
                            type="button"
                            className="btn btn-secondary"
                            data-bs-dismiss="modal"
                          >
                            Cancelar
                          </button>
                          <button
                            type="submit"
                            className=" btn btn-success p-2"
                            data-bs-toggle="collapse"
                            href="#pesosvl"
                            role="button"
                            aria-expanded="false"
                            aria-controls="pesosvl"
                            data-bs-dismiss="modal"
                            onClick={darDeBaja}
                          >
                            Dar de Baja
                          </button>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        ) : (
          <></>
        )}
      </div>
      <div
        className="modal fade"
        id="exampleModal"
        aria-labelledby="exampleModalLabel"
        aria-hidden="true"
      >
        <div className="modal-dialog">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title" id="exampleModalLabel">
                Generar Guía
              </h5>
              <button
                type="button"
                className="btn-close"
                data-bs-dismiss="modal"
                aria-label="Close"
              ></button>
            </div>
            <div className="modal-body">...</div>
            <div className="modal-footer">
              <button type="button" className="btn btn-primary">
                Guardar PDF
              </button>
              <button type="button" className="btn btn-secondary">
                Imprimir Guias
              </button>
            </div>
          </div>
        </div>
      </div>
      <Dashfooter />
      {/* showtoSalve
            ? "guias-print d-print-block"
            : "guias-print d-none d-print-block" */}
      {CurrentGuia?.remitente ? (
        <div
          id="current-guia-salve"
          className={
            values?.showDownload == true
              ? "p-1 mt-2 guias-print"
              : "p-1 mt-2 guias-print d-none d-print-block"
          }
        >
          <EstructuraGuia guia={CurrentGuia} />
        </div>
      ) : (
        <></>
      )}
    </div>
  );
}

export default BuscarGuias;
