import React, { useEffect } from "react";
import { useSelector } from "react-redux";
import { useFormBuscarUsuario } from "../../../../hooks/use_form_buscarUsuario";
import Spinner from "../../../spinner/spinner";
import "../buscar_usuario/buscar_usuarios.css";

function BuscarUsuarios() {
  const {
    users: { currentUser, fetching, listUsers }
  } = useSelector(state => state);
  const [values, findValues, handleInputChange, onClickChangeHandler, handlerBlurEmail, handlerInputFind, buscarUsuario, handlerChangeUser, setValues, eliminarUsuario] = useFormBuscarUsuario(currentUser)
  const { campo, campoValue } = findValues;
  const { errorMsg, rol, nombre, email, codigoPostal, dni, telefPrincipal, direccion, telef1, provincia, telef2, canton, parroquia, errorEmail } = values;

  useEffect(() => {
    console.log("Dentro de useEffect BUSCAR USUARIO")
    if (currentUser.nombre !== "") {
      setValues(currentUser);
    }
  }, [currentUser, setValues])
  return (
    <div className="crearGuias col-md-9 ms-sm-auto col-lg-10 px-md-4">
      <div className="container-fluid pt-4">
        <h4 className="title-separtor">Busqueda de Usuarios</h4>
        <hr />
        <div className="card card-body">
          <div className="row align-items-center">
            <div className="col-sm-12 col-md-2">
              <b className="titleSection-form">Buscar por:</b>
            </div>
            <div className="col-sm-12 col-md-3">
              <select className="form-select" name="campo" value={campo} onChange={handlerInputFind}>
                <option defaultValue="nombre" >Nombre</option>
                <option defaultValue="dni">Cedula/DNI</option>
                <option defaultValue="email" >Correo</option>
              </select>
            </div>

            <form className="col-sm-12 col-md-7" onSubmitCapture={buscarUsuario}>
              <div className="input-group">
                <input
                  type="text"
                  className="form-control dropdown-toggle"
                  placeholder="Buscar ..."
                  name="campoValue"
                  value={campoValue}
                  onChange={handlerInputFind}
                />
                <span className="mdi mdi-magnify search-icon" />
                {
                  fetching ?
                    <Spinner />
                    :
                    <button className="btn-solid-secondary px-4 " type="submit">
                      Buscar
                </button>
                }
              </div>
            </form>
          </div>
        </div>
        <div className="card card-body">
          <div className="row align-items-center">
            <div className="col-sm-12 col-md-8">
              <h5 className="titleSection-form">Usuarios</h5>
            </div>
          </div>
          <hr />
          <div className="row align-items-start">
            <div className="row table-cm">
              <table className="table dt-responsive nowrap">
                <thead>
                  <tr className="header-table">
                    <th>ID</th>
                    <th>Nombre</th>
                    <th>Email</th>
                    <th>Rol</th>
                    {/* <th>DNI</th> */}
                    <th />
                    <th />
                    <th />
                  </tr>
                </thead>

                <tbody>
                  {
                    listUsers.map((user) =>
                      <tr key={user.id} className="hover-table">
                        <td>{user.id}</td>
                        <td>{user.nombre.toUpperCase()}</td>
                        <td>{user.email}</td>
                        <td>{user.rol}</td>
                        {/* <td>{user.dni}</td> */}
                        <td>
                          <button type="button" className=" btn-dt-del w-100" onClickCapture={(e) => eliminarUsuario(e, user.id, user.email)} >
                            Eliminar
                      </button>
                        </td>
                        <td>
                          <button type="button" className=" btn-dt-details w-100" onClickCapture={() => handlerChangeUser(user)}>
                            {/* <button type="button" className=" btn-dt-details w-100"> */}
                            Detalles
                      </button>
                        </td>
                      </tr>
                    )
                  }

                </tbody>
              </table>
              {/* <div className='no-Item'>
								<p className="text-muted noGuias">No hay Guias Registradas</p>
							</div> */}

            </div>
          </div>
        </div>
        <div className="col-sm-12 col-md-6 align-self-start">
          <h4 className="title-separtor pt-3">Detalles Usuario</h4>
        </div>
        <hr />
        <div className="card card-body">
          {/* <div className='no-Item'>
						<p className="text-muted noGuias">No hay guia seleccionada</p>
					</div> */}
          <h5 className="titleSection-form">Datos de Usuario</h5>
          <hr />
          <div className="row ">
            <div className="col-lg-6 ">
              <form>
                <div className="mb-3">
                  <label htmlFor="simpleinput" className="form-label-input">
                    Nombre Completo
                  </label>
                  <input
                    type="text"
                    // 
                    className="form-control"
                    placeholder="Nombres"
                    value={nombre}
                    name="nombre"
                    onChange={handleInputChange}
                  />
                </div>

                <div className="mb-3">
                  <label htmlFor="simpleinput" className="form-label-input">
                    Ruc/Dni
                  </label>
                  <input
                    type="text"

                    className="form-control"
                    placeholder="110XXXXXXXXX"
                    value={dni}
                    name="dni"
                    onChange={handleInputChange}
                  />
                </div>
                <div className="mb-3">
                  <label htmlFor="simpleinput" className="form-label-input">
                    Dirección
                  </label>
                  <input
                    type="text"

                    className="form-control"
                    placeholder="Praza Ramón, 31, 8º F"
                    value={direccion}
                    name="direccion"
                    onChange={handleInputChange}
                  />
                </div>
                <div className="mb-3">
                  <label className="form-label-input">
                    Provincia
                  </label>
                  <select className="form-select" value={provincia}
                    name="provincia"
                    onChange={handleInputChange}>
                    <option defaultValue="Loja" >Loja</option>
                    <option defaultValue="Zamora">Zamora</option>
                    <option defaultValue="Cuenca">Cuenca</option>
                    <option defaultValue="Imbabura">Imbabura</option>
                    <option defaultValue="Los Rios">Los Rios</option>
                  </select>
                </div>
                <div className="mb-3">
                  <label className="form-label-input">
                    Canton
                  </label>
                  <select className="form-select" value={canton}
                    name="canton"
                    onChange={handleInputChange}>
                    <option defaultValue="Loja">Loja</option>
                    <option defaultValue="Zamora">Zamora</option>
                    <option defaultValue="Cuenca">Cuenca</option>
                    <option defaultValue="Imbabura">Imbabura</option>
                    <option defaultValue="Los Rios">Los Rios</option>
                  </select>
                </div>
                <div className="mb-3">
                  <label htmlFor="simpleinput" className="form-label-input">
                    Parroquia
                  </label>
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Praza Ramón"
                    value={parroquia}
                    name="parroquia"
                    onChange={handleInputChange}
                  />
                </div>
              </form>
            </div>

            <div className="col-lg-6 ">
              <form>
                <div className="mb-3">
                  <label htmlFor="simpleinput" className="form-label-input">
                    Email
                  </label>
                  <input
                    type="email"
                    className="form-control"
                    id="inputEmail"
                    placeholder="example@example.com"
                    value={email}
                    name="email"
                    onChange={handleInputChange}
                    onBlurCapture={handlerBlurEmail}
                  />
                  {
                    errorEmail && errorEmail !== "" ?
                      <div className="container-error">
                        <p className="text-error">{errorEmail}</p>
                      </div>
                      : <></>

                  }
                </div>
                <div className="mb-3">
                  <label htmlFor="simpleinput" className="form-label-input">
                    Código Postal
                  </label>
                  <input
                    type="text"

                    className="form-control"
                    placeholder="12514"
                    value={codigoPostal}
                    name="codigoPostal"
                    onChange={handleInputChange}
                  />
                </div>

                <div className="mb-3">
                  <label htmlFor="simpleinput" className="form-label-input">
                    Télefono Principal
                  </label>
                  <input
                    type="text"

                    className="form-control"
                    placeholder="Apellidos Completos"
                    value={telefPrincipal}
                    name="telefPrincipal"
                    onChange={handleInputChange}
                  />
                </div>
                <div className="mb-3">
                  <label htmlFor="simpleinput" className="form-label-input">
                    Télefono 1
                  </label>
                  <input
                    type="text"

                    className="form-control"
                    placeholder="Apellidos Completos"
                    value={telef1}
                    name="telef1"
                    onChange={handleInputChange}
                  />
                </div>
                <div className="mb-3">
                  <label htmlFor="simpleinput" className="form-label-input">
                    Télefono 2
                  </label>
                  <input
                    type="text"

                    className="form-control"
                    placeholder="Apellidos Completos"
                    value={telef2}
                    name="telef2"
                    onChange={handleInputChange}
                  />
                </div>
                <div className="mb-3">
                  <label htmlFor="simpleinput" className="form-label-input">
                    Rol
                  </label>
                  <select
                    className="col  align-self-start form-select"

                    placeholder="Tipo Usuario"
                    value={rol}
                    onChange={handleInputChange}
                  >
                    <option defaultValue="admin">Administrador</option>
                    <option defaultValue="puntoVenta" >Puntos de Venta</option>
                    <option defaultValue="sucursal">Sucursales</option>
                    <option defaultValue="coorporativo">Clientes Coorporativos</option>
                  </select>
                </div>

              </form>
            </div>
            {
              errorMsg && errorMsg !== "" ?
                <div className="container-error">
                  <p className="text-error">{errorEmail}</p>
                </div> : <></>
            }
          </div>
        </div>
        <div className="card card-body">
          <div className="row">
            <div className="col mx-1 p-2 bt-a"></div>
            <button type="button" className="btn-dt-details col mx-1 bt-a p-2">
              Guardar Cambios
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}

export default BuscarUsuarios;
