import React from "react";
import { ParseDate } from "../../../../helpers/helper";
import AsignacionManifiesto from "../../../../hooks/asignacion_manifiesto";
import "../asignacion_correspondencia/asignacion_correspondencia.css";
import { DebounceInput } from "react-debounce-input";
import Spinner from "../../../spinner/spinner";
import Dashfooter from "../../../dash-componets/footter/dashfooter";

function AsignacionCorrespondencia() {
  const [
    nameCartero,
    listGuiasAsignacion,
    fetching,
    setAsignaciondb,
    deletefromfromList,
    handlerSearchGuia,
    setnameCartero,
    updateCartero,
  ] = AsignacionManifiesto();

  return (
    <div className="crearGuias col-md-9 ms-sm-auto col-lg-10 px-md-4">
      <div className="container-fluid pt-4">
        <h4 className="title-separtor">Asignacion de Correspondencias</h4>

        <hr />
        <div className="card card-body">
          <h5 className="titleSection-form pt-4">Asingnacion</h5>
          <hr />
          <div className="row ">
            <div className="col-sm-12 col-md-4">
              <div className="mb-3">
                <label className="form-label-input">
                  Seleccionar Agente de Entrega
                </label>
                <select
                  onChange={updateCartero}
                  value={nameCartero}
                  className="form-select"
                >
                  <option value="">Selecciona un Encargado</option>
                  <option value="Diego Fernando">Diego Fernando</option>
                  <option value="Santiago Moreno">Santiago Moreno</option>
                  <option value="Carlos Rapifas">Carlos Rapifas</option>
                </select>
              </div>
            </div>
          </div>
          <hr />
          <div className="row ">
            <div className="col-sm-12 col-md-6">
              <form>
                <div className="input-group">
                  <label className="form-label-input">Buscar Guia</label>
                  <DebounceInput
                    minLength={0}
                    debounceTimeout={300}
                    type="text"
                    className="form-control dropdown-toggle"
                    placeholder="Num. de Guia..."
                    id="top-search"
                    onChange={handlerSearchGuia}
                  />
                  <span className="mdi mdi-magnify search-icon" />

                  {fetching ? (
                    <div className="container-spinner-buscar">
                      <Spinner />
                    </div>
                  ) : (
                    <button
                      className="btn-solid-secondary px-4 "
                      type="submit"
                      // onClick={searchGuias}
                    >
                      Buscar
                    </button>
                  )}
                </div>
              </form>
            </div>
          </div>
        </div>

        <div className="card card-body">
          <div className="row align-items-center">
            <div className="col-sm-12 col-md-8">
              <h5 className="titleSection-form">Lista de Entrega</h5>
            </div>
          </div>
          <hr />
          <div className="row align-items-start">
            <div className="row table-cm">
              <table className="table dt-responsive nowrap">
                <thead>
                  <tr className="header-table">
                    <th>ID</th>
                    <th>Fecha</th>
                    <th>Origen</th>
                    <th>Destino</th>
                    <th>Estado</th>
                    <th>Eliminar</th>
                    {/* <th /> */}
                  </tr>
                </thead>

                <tbody>
                  {listGuiasAsignacion?.map((guia) => {
                    return (
                      <tr key={`origen_${guia.idGuia}`} className="hover-table">
                        <td>{guia.idGuia}</td>
                        <td>{ParseDate(guia.fechaActual)}</td>
                        <td>
                          {`${guia.remitente.provincia}-${guia.remitente.canton}`}
                        </td>
                        <td>
                          {`${guia.destinatario.provincia}-${guia.destinatario.canton}`}
                        </td>
                        <td>{guia.estado}</td>
                        <td>
                          <div
                            type="button"
                            className=" "
                            onClick={() => deletefromfromList(guia)}
                          >
                            <i className="large material-icons">
                              delete_forever
                            </i>
                          </div>
                        </td>
                      </tr>
                    );
                  })}
                </tbody>
              </table>
              {listGuiasAsignacion.length === 0 ? (
                <div className="no-Item">
                  <p className="text-muted noGuias">No hay Guias Registradas</p>
                </div>
              ) : (
                <></>
              )}
            </div>
          </div>
        </div>
        <div className="card card-body">
          <div className="row">
            <button type="button" className="btn-dt col mx-1">
              Cancelar Entrega
            </button>
            <button
              type="button"
              className="btn btn-success col mx-1"
              onClick={setAsignaciondb}
            >
              Guardar Asignación
            </button>
          </div>
        </div>
      </div>
      <Dashfooter />
    </div>
  );
}

export default AsignacionCorrespondencia;
