import { useState } from 'react';
import { useDispatch } from 'react-redux';
import { validarEmail } from '../helpers/helper';
import { startLogin } from '../redux/actions/auth_actions';
import { useToasts } from "react-toast-notifications";
import { useHistory } from 'react-router';

export const useFormLogin = (initialState = {}) => {
    const dispatch = useDispatch();
    const history = useHistory()
    const [values, setValues] = useState(initialState);
    const { addToast } = useToasts();

    const reset = () => {
        setValues(initialState);
    }

    const handleInputChange = ({ target }) => {
        setValues({
            ...values,
            [target.name]: target.value
        });
    }
    const msgError = (title) => {
        return addToast(title, {
            appearance: "error",
            autoDismiss: true,
            autoDismissTimeout: 4000,
        });
    }
    const handlerBlurEmail = () => {
        console.log("ANTES DE COMPROBAR")
        if (validarEmail(values.email)) {
            setValues({
                ...values,
                errorEmail: ""
            })
        } else {
            setValues({
                ...values,
                errorEmail: "El email no es válido"
            })
        }
    }

    const onClickChangeHandler = (e) => {
        e.preventDefault();
        if (values.errorEmail === "") {
            if (values.email !== "" &&
                values.password !== "") {
                console.log("===> ANTES DE START LOGIN");
                dispatch(startLogin(values.email, values.password, reset, msgError, history))
            }
        } else {
            // Existen errores
            msgError("Existen Errores en el formulario")
        }
    }
    return [values, , handleInputChange, onClickChangeHandler, handlerBlurEmail];
}