import { useState } from "react";
import { useToasts } from "react-toast-notifications";
import { useDispatch, useSelector } from "react-redux";
import Swal from "sweetalert2";
import { v1 as uuid } from "uuid";
import {
  addGuiaManifiestos,
  addIdGuiaManifiesto,
  crearManifiesto,
  deleteGuiaManifiesto,
  deleteguiaSearch,
  deleteIdGuiaManifiesto,
  findGuiaManifiesto,
  setDestiono,
} from "../redux/actions/manifiestos_actions";

function CrearManifiestoHook(initialValues) {
  const {
    manifiestos: {
      listGuiasManifiesto,
      fetching,
      provinciaDestino,
      idGuiasManifiesto,
    },
  } = useSelector((state) => state);

  const dispatch = useDispatch();

  const { addToast } = useToasts();

  const [values, setvalues] = useState(initialValues);
  const [searchText, setsearchText] = useState("");
  const [showErrorDestino, setshowErrorDestino] = useState(false);
  const [listIDActualizados, setlistIDActualizados] = useState([]);

  const swalWithBootstrapButtons = Swal.mixin({
    customClass: {
      confirmButton: "btn btn-success",
      cancelButton: "btn btn-danger",
    },
    buttonsStyling: false,
  });

  const showAlet = (provincia, idGuia) => {
    return swalWithBootstrapButtons
      .fire({
        title: "Quieres Agregar esta Guía?",
        text: `Esta guia pertenece a la provincia de ${provincia}, y el manifiesto a ${provinciaDestino} deseas agregarla a la lista`,
        icon: "warning",
        showCancelButton: true,
        confirmButtonText: "Agregar al Manifiesto",
        cancelButtonText: "No",
        reverseButtons: true,
      })
      .then((result) => {
        if (result.isConfirmed) {
          // console.log("agregar");
          dispatch(addIdGuiaManifiesto(idGuia));
          dispatch(addGuiaManifiestos());
          dispatch(deleteguiaSearch());
          toastSms("Guia Agregada", "success");
          setsearchText("");
        } else if (result.dismiss === Swal.DismissReason.cancel) {
          dispatch(deleteguiaSearch());
          setsearchText("");
          borrarIdGuia(idGuia);
        }
      });
  };

  //mensajes de alerta

  const toastSms = (text, type) => {
    return addToast(text, {
      appearance: type,
      autoDismiss: true,
      autoDismissTimeout: 3000,
    });
  };

  //mensajes de alerta fin

  const handlerChange = (e) => {
    e.preventDefault();

    setvalues({
      ...values,
      [e.target.name]: e.target.value,
    });
    if (e.target.name === "destino") {
      console.log(e.target.name);
      dispatch(setDestiono(e.target.value));
    }
  };
  //comprueba la lista si se agrega o no
  const comprobarGuia = (provincia, idGuia) => {
    if (provinciaDestino === provincia) {
      dispatch(addGuiaManifiestos());
      dispatch(deleteguiaSearch());
      toastSms("Guia Agregada", "success");
      setsearchText("");
    } else {
      showAlet(provincia, idGuia);
    }
  };
  const agregaridalalista = (idGuia) => {
    // setlistIDActualizados([...listIDActualizados, idGuia]);
    dispatch(addIdGuiaManifiesto(idGuia));
  };
  const errorsms = () => {
    toastSms("Error al buscar guia", "error");
  };
  const errorsmsPeso = () => {
    // toastSms("Guias sin actualización de peso", "info");
    setsearchText("");
    return addToast("Guias sin actualización de peso", {
      appearance: "info",
      autoDismiss: true,
      autoDismissTimeout: 4000,
    });
  };
  //se utiliza de forma manual para buscar guias

  const searchGuias = (e, from) => {
    e.preventDefault();
    setshowErrorDestino(false);
    if (from === "handler") {
      if (idGuiasManifiesto.includes(e.target.value.trim())) {
        toastSms("Esta guia ya fue agregada", "error");
        setsearchText("");
      } else {
        dispatch(
          findGuiaManifiesto(
            e.target.value,
            errorsms,
            comprobarGuia,
            agregaridalalista,
            errorsmsPeso
          )
        );
      }
    } else {
      if (idGuiasManifiesto.includes(searchText.trim())) {
        toastSms("Esta guia ya fue agregada", "error");
        setsearchText("");
      } else {
        dispatch(
          findGuiaManifiesto(
            searchText,
            errorsms,
            comprobarGuia,
            agregaridalalista,
            errorsmsPeso
          )
        );
      }
    }
  };

  //se ecuta al finalizar de escribir
  const handlerSearchText = (e) => {
    e.preventDefault();
    // console.log(e.target.value);
    setsearchText(e.target.value);

    if (provinciaDestino === "") {
      toastSms("Pimero seleccione un destino", "error");
      setshowErrorDestino(true);
    } else {
      setshowErrorDestino(false);
      if (e.target.value.trim()) {
        searchGuias(e, "handler");
      }
    }
  };
  const borrarIdGuia = (idGuiaDelete) => {
    let listtemp = idGuiasManifiesto.filter((guia) => idGuiaDelete !== guia);
    // setlistIDActualizados(listtemp);
    dispatch(deleteIdGuiaManifiesto(listtemp));
  };
  const deleteguiaFromManifiesto = (idGuia) => {
    borrarIdGuia(idGuia);
    dispatch(deleteGuiaManifiesto(idGuia));
  };

  //creaciond e manifiestos en db

  const successMesaje = () => {
    return toastSms("manifiesto creado con exito", "success");
  };
  const errorMesaje = () => {
    return toastSms("manifiesto creado con exito", "success");
  };

  const enviarManifiestos = () => {
    let listIdtemp = [];
    idGuiasManifiesto.forEach((idGuia) => {
      listIdtemp = [
        ...listIdtemp,
        {
          idGuia: idGuia,
          estado: "creado",
        },
      ];
    });
    let tempManifiesto = {
      idManifiesto: values.idManifiesto,
      estado: "creado",
      manifiestoCreadoPor: "Sucursal",
      fechaCreacion: Date.now(),
      numEnvios: idGuiasManifiesto.length,
      provinciaDestino: provinciaDestino,
      listIdGuiasenEnviar: listIdtemp,
    };
    dispatch(
      crearManifiesto(
        idGuiasManifiesto,
        tempManifiesto,
        successMesaje,
        errorMesaje
      )
    );
    setvalues({
      idManifiesto: uuid(),
      fechaCreacionManifiesto: Date.now(),
      destino: "",
    });
    setlistIDActualizados([]);
  };

  return [
    fetching,
    values,
    searchText,
    showErrorDestino,
    listGuiasManifiesto,
    provinciaDestino,
    enviarManifiestos,
    deleteguiaFromManifiesto,
    handlerChange,
    searchGuias,
    handlerSearchText,
  ];
}

export default CrearManifiestoHook;
