import React, { useCallback, useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { actualizarRol, consultarRoles } from '../../../../redux/actions/roles_actions';
import '../asignacion_acciones/asignacion-acciones.css';
import { useToasts } from "react-toast-notifications";

function AsignacionAcciones() {
	const dispatch = useDispatch();
	const [user, setUser] = useState("0")
	const [funciones, setDataRol] = useState({})
	const [id, setId] = useState("");
	const [val, setVal] = useState(false);
	const { addToast } = useToasts();

	const {
		roles: {
			listRoles,
			fetching
		}
	} = useSelector((state) => state);

	const changeValue = ({ target }) => {
		setUser(target.value)
		listRoles?.forEach((rol) => {
			if (rol.rol === target.value) {
				setDataRol(rol.funciones);
				setId(rol.id)
			}
		})
	}
	const msgOk = () => {
		return addToast("Funciones actualizadas con éxito", {
			appearance: "success",
			autoDismiss: true,
			autoDismissTimeout: 3000,
		})
	}
	const msgError = (title) => {
		return addToast(title, {
			appearance: "error",
			autoDismiss: true,
			autoDismissTimeout: 3000,
		});
	}

	const actualizarAsignaciones = () => {
		if (user === "0") {
			msgError("Debes de seleccionar un rol")
		} else {
			dispatch(actualizarRol(id, funciones, msgError, msgOk))
		}
	}
	const handleChange = (e) => {
		setVal(!val);
		const tempMap = funciones;
		console.log(tempMap);
		tempMap[e] = !funciones[e];
		setDataRol(tempMap);
	}
	const buscar = useCallback(() => {
		dispatch(consultarRoles());
	}, [dispatch])

	useEffect(() => {
		console.log("=======> DENTRO DE ASIGNACIONES-ACCIONES.JS USEEFFECT")

		if (listRoles === null && !fetching) {
			buscar();
		}
	}, [listRoles, fetching, buscar])

	return (
		<div className="crearGuias col-md-9 ms-sm-auto col-lg-10 px-md-4">
			<div className="container-fluid pt-4">
				<h4 className="title-separtor">Asignacion Acciones</h4>
				<hr />
				<div className="card card-body">
					<div className="row ">
						<div className="col-lg-6 ">
							<form>
								<div className="mb-3">
									<label className="form-label-input">
										Tipo Usuario
									</label>
									<select className="form-select"
										value={user} onChange={changeValue}>
										<option value="0">Seleccionar</option>
										{
											listRoles?.map((rol) =>
												<option key={rol.id} value={rol.rol}>
													{rol.rol === "admin" ? "Administrador" :
														rol.rol === "puntoVenta" ? "Punto de Venta" :
															rol.rol === "sucursal" ? "Sucursal" : "Cliente Coorporativo"
													}
												</option>

											)
										}
									</select>
								</div>
							</form>
						</div>
					</div>
					<h5 className="titleSection-form">Acciones</h5>
					<hr />
					<div className="row ">
						<div className="col-lg-3 ">
							<label className="form-label-input">
								Guias
							</label>
							<div className="mt-3">
								<div className="form-check text-start">
									<input type="checkbox" className="form-check-input" id="customCheck3" name="g_buscar" checked={funciones?.g_buscar} onChange={() => handleChange("g_buscar")} />
									<label className="form-check-label" htmlFor="customCheck3">
										Buscar
									</label>
								</div>
								<div className="form-check text-start">
									<input type="checkbox" className="form-check-input" id="customCheck1" name="g_crear" checked={funciones?.g_crear} onChange={() => handleChange("g_crear")} />
									<label className="form-check-label " htmlFor="customCheck1">
										Crear
									</label>
								</div>
								<div className="form-check text-start">
									<input type="checkbox" className="form-check-input" id="customCheck2" name="g_editar" checked={funciones?.g_editar} onChange={() => handleChange("g_editar")} />
									<label className="form-check-label" htmlFor="customCheck2">
										Editar
									</label>
								</div>
								<div className="form-check text-start">
									<input type="checkbox" className="form-check-input" id="customCheck4" name="g_eliminar" checked={funciones?.g_eliminar} onChange={() => handleChange("g_eliminar")} />
									<label className="form-check-label" htmlFor="customCheck4">
										Eliminar
									</label>
								</div>
								<div className="form-check text-start">
									<input type="checkbox" className="form-check-input" id="customCheck5" name="g_anular" checked={funciones?.g_anular} onChange={() => handleChange("g_anular")} />
									<label className="form-check-label" htmlFor="customCheck5">
										Anulacion de Guias
									</label>
								</div>
								<div className="form-check text-start">
									<input type="checkbox" className="form-check-input" id="customCheck6" name="g_actualizarPeso" checked={funciones?.g_actualizarPeso} onChange={() => handleChange("g_actualizarPeso")} />
									<label className="form-check-label" htmlFor="customCheck6">
										Actualizar Pesos
									</label>
								</div>
							</div>
						</div>
						<div className="col-lg-3 ">
							<label className="form-label-input">
								Manifiestos
							</label>
							<div className="mt-3">
								<div className="form-check text-start">
									<input type="checkbox" className="form-check-input" id="customCheck7" name="m_crear" checked={funciones?.m_crear} onChange={() => handleChange("m_crear")} />
									<label className="form-check-label" htmlFor="customCheck7">
										Crear
									</label>
								</div>
								<div className="form-check text-start">
									<input type="checkbox" className="form-check-input" id="customCheck8" checked={funciones?.m_receptar} onChange={() => handleChange("m_receptar")} />
									<label className="form-check-label " htmlFor="customCheck8">
										Receptar Manifiestos
									</label>
								</div>
								<div className="form-check text-start">
									<input type="checkbox" className="form-check-input" id="customCheck9" name="m_asignar" checked={funciones?.m_asignar} onChange={() => handleChange("m_asignar")} />
									<label className="form-check-label" htmlFor="customCheck9">
										Asignar Correspondencia
									</label>
								</div>
								<div className="form-check text-start">
									<input type="checkbox" className="form-check-input" id="customCheck10" name="m_registrar" checked={funciones?.m_registrar} onChange={() => handleChange("m_registrar")} />
									<label className="form-check-label" htmlFor="customCheck10">
										Registrar Correspondencia
									</label>
								</div>
							</div>
						</div>
						<div className="col-lg-3 ">
							<label className="form-label-input">
								Gestion de Usuarios
							</label>
							<div className="mt-3">
								<div className="form-check text-start">
									<input type="checkbox" className="form-check-input" id="customCheck11" name="u_crear" checked={funciones?.u_crear} onChange={() => handleChange("u_crear")} />
									<label className="form-check-label" htmlFor="customCheck11">
										Crear
									</label>
								</div>
								<div className="form-check text-start">
									<input type="checkbox" className="form-check-input" id="customCheck12" name="u_buscar" checked={funciones?.u_buscar} onChange={() => handleChange("u_buscar")} />
									<label className="form-check-label " htmlFor="customCheck12">
										Buscar
									</label>
								</div>
								<div className="form-check text-start">
									<input type="checkbox" className="form-check-input" id="customCheck13" name="u_editar" checked={funciones?.u_editar} onChange={() => handleChange("u_editar")} />
									<label className="form-check-label" htmlFor="customCheck13">
										Editar
									</label>
								</div>
								<div className="form-check text-start">
									<input type="checkbox" className="form-check-input" id="customCheck14" name="u_eliminar" checked={funciones?.u_eliminar} onChange={() => handleChange("u_eliminar")} />
									<label className="form-check-label" htmlFor="customCheck14">
										Eliminar
									</label>
								</div>
							</div>
						</div>
						<div className="col-lg-3 ">
							<label className="form-label-input">
								Gestion de Plataforma
							</label>
							<div className="mt-3">
								<div className="form-check text-start">
									<input type="checkbox" className="form-check-input" id="customCheck15" name="p_asignacion" checked={funciones?.p_asignacion} onChange={() => handleChange("p_asignacion")} />
									<label className="form-check-label" htmlFor="customCheck15">
										Asignacion de Acciones
									</label>
								</div>
							</div>
						</div>
					</div>
				</div>

				<div className="card card-body">
					<div className="row">
						<button type="button" className="btn-dt col mx-1">
							Cancelar
						</button>
						<button type="button" className="btn btn-success col mx-1" onClickCapture={() => actualizarAsignaciones()}>
							Guardar
						</button>
					</div>
				</div>

				{/* <div className="card card-body">
					<div className="row">
						<button type="button" className=" btn-dt-del col mx-1 p-2 bt-a">
							Cancelar
						</button>
						<button type="button" className=" btn-dt-details col mx-1 bt-a">
							Anular
						</button> */}
				{/* <button type="button" className="btn-dt col mx-1">Elimina</button>
                    <button type="button" className="btn btn-success col mx-1">Imprimir</button> */}
				{/* </div>
				</div> */}
			</div>
		</div>
	);
}

export default AsignacionAcciones;
