import React, { useEffect, useCallback, useRef, useState } from 'react'
import "../Calculadora/calculadora.css"
import ComponentCotizacion from './components/components_cotizacion';
import ComponentRuta from './components/components_ruta';
import ComponentTipo from './components/component_tipo';
import { coverturasList } from "./costos";


const Calculadora = () => {
    const [mostrarProvincia, setMostrarProvincia] = useState(false);
    // const [provincia, setProvincia] = useState('');
    const [covertura, setCovertura] = useState({});
    const [title, setTitle] = useState('');
    const [description, setDescription] = useState('');
    const [total, setTotal] = useState(0.0);
    const [listaCoverturas, setListaCoverturas] = useState([]);
    const costoDosK = useRef();
    const costoKAdicional = useRef();
    const peso = useRef();

    useEffect(() => {
        console.log("=======> DENTRO DE CALCULADORA.JS USEEFFECT")
        setListaCoverturas(coverturasList.sort(compare));
    }, []);

    function compare(a, b) {
        if (a.Destino > b.Destino) {
            return 1
        } else if (a.Destino < b.Destino) {
            return -1;
        }
        return 0;
    }

    const costoKm = useCallback((trayecto) => {
        switch (trayecto) {
            case 'Local':
                costoDosK.current = 1.98;
                costoKAdicional.current = 0.48;
                // return {
                //     'costoDosK': 1.98,
                //     'costoKAdicional': 0.48,
                // };
                break;
            case 'Provincial':
                costoDosK.current = 3.27;
                costoKAdicional.current = 0.68;
                // return {
                //     'costoDosK': 3.27,
                //     'costoKAdicional': 0.68,
                // };
                break;
            case 'Nacional Primario':
                costoDosK.current = 3.95;
                costoKAdicional.current = 0.75;
                // return {
                //     'costoDosK': 3.95,
                //     'costoKAdicional': 0.75,
                // };
                break;
            case 'Nacional Secundario':
                costoDosK.current = 3.95;
                costoKAdicional.current = 0.75;
                // return {
                //     'costoDosK': 3.95,
                //     'costoKAdicional': 0.75,
                // };
                break;
            case 'Especial':
                costoDosK.current = 5.94;
                costoKAdicional.current = 0.92;
                // return {
                //     'costoDosK': 5.94,
                //     'costoKAdicional': 0.92,
                // };
                break;
            case 'Región Insular':
                costoDosK.current = 10.10;
                costoKAdicional.current = 2.53;
                // return {
                //     'costoDosK': 10.10,
                //     'costoKAdicional': 2.53,
                // };
                break;
            default:
                costoDosK.current = 0.0;
                costoKAdicional.current = 0.0;
                // return {
                //     'costoDosK': 0.0,
                //     'costoKAdicional': 0.0,
                // };
                break;
        }
    }, [])

    const handleChangeCiudadDestino = useCallback((e) => {
        e.preventDefault();
        coverturasList.forEach((covertura) => {
            if (covertura.Destino === e.target.value) {
                // setProvincia(covertura.Provincia)
                setCovertura(covertura);
                costoKm(covertura.Trayecto);
                if (title === "Documento") {
                    setTotal(costoDosK.current)
                }
                if (title === "Paquete") {
                    if (peso.current === 0 || peso.current === '') {
                        setTotal(0.0)
                    } else {
                        if (parseFloat(peso.current) <= 2.99) {
                            setTotal(costoDosK.current)
                        } else {
                            var difPeso = parseFloat(peso.current) - 2;
                            const totalCalculo = difPeso * costoKAdicional.current;
                            setTotal(totalCalculo + costoDosK.current)
                        }
                    }
                }
            }
        });
        setMostrarProvincia(true);
    }, [costoKm, title, peso])

    const handleChangleTipo = useCallback((titulo, descripcion) => {
        if (titulo === "Documento") {
            setTotal(costoDosK.current)
        }
        setTitle(titulo)
        setDescription(descripcion)
    }, [])

    const handleChangeCalculo = useCallback((e) => {
        if (e.target.value === '') {
            setTotal(0.0)
        } else {
            peso.current = parseFloat(e.target.value);
            if (parseFloat(e.target.value) <= 2.99) {
                setTotal(costoDosK.current)
            } else {
                var difPeso = parseFloat(e.target.value) - 2;
                const totalCalculo = difPeso * costoKAdicional.current
                setTotal(totalCalculo + costoDosK.current)
            }

        }
    }, [])

    return (
        <div className='col container-calculator'>
            <div className="form-container">
                <div className='row d-flex flex-wrap align-items-center'>
                    {/* CIUDAD DE ORIGEN, DESTINO Y PROVINCIA */}
                    <ComponentRuta
                        coverturasList={listaCoverturas}
                        handleChangeCiudadDestino={handleChangeCiudadDestino}
                        mostrarProvincia={mostrarProvincia}
                        covertura={covertura}
                    />
                </div>

                {/* TIPO */}
                {
                    Object.keys(covertura).length === 0 ?
                        <></>
                        :
                        <ComponentTipo
                            description={description}
                            handleChangleTipo={handleChangleTipo}
                            title={title}
                        />
                }

                {/* Cotización */}
                <ComponentCotizacion
                    title={title}
                    total={total}
                    handleChangeCalculo={handleChangeCalculo}
                />
            </div>
        </div>
    )
}

export default Calculadora
