import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useToasts } from "react-toast-notifications";
import {
  actualizarManifiestosyGuias,
  comprobarItems,
  findManifiesto,
  setSearchIdGuiaManifiesto,
  updateGuiaStatus,
} from "../redux/actions/manifiestos_actions";

function RecepcionManifiestoHook() {
  const {
    manifiestos: {
      msgError,
      fetching,
      currentManifiesto,
      searchIdReseptarGuia,
      numRecibidos,
      numMalEncaminados,
      numPendientes,
    },
  } = useSelector((state) => state);

  const dispatch = useDispatch();

  const { addToast } = useToasts();
  const [values, setvalues] = useState({
    idManifiesto: "",
    idGuia: "",
    itemResividos: 0,
    malEncaminados: 0,
    pendientes: 0,
  });

  const toastSms = (text, type) => {
    return addToast(text, {
      appearance: type,
      autoDismiss: true,
      autoDismissTimeout: 3000,
    });
  };

  //se ecuta al finalizar de escribir
  const handlerSearchText = (e) => {
    e.preventDefault();
    setvalues({
      ...values,
      [e.target.name]: e.target.value,
    });
  };

  const setIdGuiaSearch = (e) => {
    console.log(e.target.value);
    e.preventDefault();
    dispatch(setSearchIdGuiaManifiesto(e.target.value));
    if (e.target.value.trim()) {
      actualizarEstadoGuia(e.target.value);
    } else {
      toastSms("No hay un Num. de guia a buscar", "error");
    }
  };
  const errorManifiesto = () => {
    return toastSms(msgError, "error");
  };
  const succesManifiesto = () => {
    return toastSms("Manifiesto encontrada", "success");
  };

  const findManifiestoHook = () => {
    // console.log("entro a la funcion");
  return  dispatch(
      findManifiesto(values?.idManifiesto, succesManifiesto, errorManifiesto)
    );
  };

  const actualizarEstadoGuia = (idGuiaitem) => {
    let tempItem = {
      idGuia: idGuiaitem,
      estado: "receptado",
    };
    dispatch(
      updateGuiaStatus(
        currentManifiesto.listIdGuiasenEnviar,
        idGuiaitem,
        tempItem
      )
    );
    toastSms("Guia Actualizada", "success");
  };
  const actualizarManifiestoGuiaDB = () => {
    const susccess = () =>
      toastSms("Manifiesto actualizacon con exito", "success");
    const error = () => toastSms(`Error al actualizar ${msgError}`, "success");
    dispatch(actualizarManifiestosyGuias(currentManifiesto, susccess, error));
  };
  return [
    values,
    fetching,
    currentManifiesto,
    numRecibidos,
    numMalEncaminados,
    searchIdReseptarGuia,
    numPendientes,
    actualizarManifiestoGuiaDB,
    handlerSearchText,
    findManifiestoHook,
    actualizarEstadoGuia,
    setIdGuiaSearch,
  ];
}

export default RecepcionManifiestoHook;
