import { guias_reducer } from "../reducers/guias_reducer";
import { createStore, combineReducers, applyMiddleware, compose } from "redux";
import thunk from "redux-thunk";
import { authReducer } from "../reducers/auth_reducer";
import { findGuiaAnuladas } from "../actions/guias_actions";
import { manifiesto_reducer } from "../reducers/manifiesto_reducer";
import { users_reducer } from "../reducers/users_reducer";
import { userFunctions } from "../actions/auth_actions";
import { rolesReducer } from "../reducers/roles_reducer";
import { consultarRoles2 } from "../actions/roles_actions";
import { pagesReducer } from "../reducers/pages_reducer";

const composeEnhancers =
  (typeof window !== "undefined" &&
    window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__) ||
  compose;

const reducers = combineReducers({
  guias: guias_reducer,
  manifiestos: manifiesto_reducer,
  auth: authReducer,
  users: users_reducer,
  roles: rolesReducer,
  pages: pagesReducer,
});

// let store = createStore(reducers, composeEnhancers(applyMiddleware(thunk)));

export default function generateStore() {
  // window.location.pathname
  let store = createStore(reducers, composeEnhancers(applyMiddleware(thunk)));
  if (window.location.pathname.includes("dashboard")) {
    // console.log("=== DENTRO DE GENERATE STORE")
    // const data = JSON.parse() ?? null
    // const accessToken = JSON.parse(localStorage.getItem("accessToken")) ?? null;
    if (localStorage.getItem("dataProfile") && localStorage.getItem("accessToken")) {
      const data = JSON.parse(localStorage.getItem("dataProfile"))
      userFunctions(store.dispatch, data.idrol);

    }
    // console.log(`generar store ====== ${window.location.pathname}`);
    // findGuiaAnuladas(store.dispatch);
  }
  return store;
}

