import React, { useState } from "react";
import "../login/login.css";
import { Link } from "react-router-dom";
import { useFormLogin } from "../../hooks/use_formLogin";
import { useSelector } from "react-redux";
import Spinner from "../spinner/spinner";

function Login() {
  const {
    auth: { fetching },
  } = useSelector((state) => state);
  const [showPass, setShowPass] = useState(false);

  const [values, , handleInputChange, onClickChangeHandler, handlerBlurEmail] =
    useFormLogin({
      email: "",
      password: "",
      errorEmail: "",
      errorMsg: "",
    });
  const { email, password, errorEmail } = values;
  return (
    <div className="body">
      <div className="container ">
        <form className="form-signin">
          <div className="img"></div>
          {/* <img
            className="mb-4"
            src={logo}
            alt=""
            height="72"
          /> */}
          <h1 className="h4 mb-3 mt-2 font-weight-normal">Ingresa tus Datos</h1>
          <p className=" text-muted">
            Bienvenido al Sistema <strong>IRIS</strong> ingresa tus datos,
            para acceder a tu panel administrativo
          </p>
          <label htmlFor="inputEmail" className="sr-only">
            Email/Usuario
          </label>
          <div className="mb-3">
            <div className="input-group">
              <input
                type="text"
                className="form-control"
                placeholder="Email / Usuario"
                aria-label=""
                aria-describedby="basic-addon1"
                name="email"
                value={email}
                onChange={handleInputChange}
                onBlurCapture={handlerBlurEmail}
              />
              {errorEmail !== "" ? (
                <div className="w-100 text-left">
                  <p className="text-error-red">{errorEmail}</p>
                </div>
              ) : (
                <></>
              )}
              {/* <button className="btn btn-outline-secondary dropdown-toggle" type="button" data-bs-toggle="dropdown" aria-haspopup="true" aria-expanded="false">Usuario</button> */}
              {/* <div className="dropdown-menu">
                <a className="dropdown-item" href="#">Administrador</a>
                <a className="dropdown-item" href="#">Punto de Venta</a>
                <a className="dropdown-item" href="#">Sucursales</a>
                <a className="dropdown-item" href="#">Cliente Coorporativos</a>
              </div> */}
            </div>
          </div>
          <div className="input-group mb-3">
            <input
              type={showPass ? "text" : "password"}
              class="form-control"
              placeholder="Contraseña"
              aria-label="Recipient's username"
              aria-describedby="basic-addon2"
              value={password}
              name="password"
              onChange={handleInputChange}
            />
            <div className="input-group-text">
              <input
                value={!showPass}
                type="checkbox"
                aria-label="Checkbox for following text input"
                onChangeCapture={() => setShowPass(!showPass)}
              />
            </div>
          </div>
          {/* <label htmlFor="inputPassword" className="sr-only">
            Contraseña
          </label>
          <input
            type="password"
            id="inputPassword"
            className="form-control"
            placeholder="Contraseña"
            required=""
            value={password}
            name="password"
            onChange={handleInputChange}

          /> */}

          {/* <div className="checkbox mb-3 text-start">
            <label>
              <input type="checkbox" value="remember-me" /> Recuérdame
            </label>
          </div> */}

          {/* <Link to='/dashboard/crearGuia'> */}
          {
            fetching ?
              <div className="w-100 d-flex justify-content-center ">
                <Spinner />
              </div>
              :
              <button className="btn  btn-primary  w-100" onClickCapture={(e) => onClickChangeHandler(e)}>
                Ingresar
          </button>
          }

          {/* </Link> */}
          <a href="/" className="btn-back">
            <p className="mt-3 mb-3 text-muted">Regresar</p>
          </a>
        </form>
      </div>
    </div>
  );
}

export default Login;
