export function updateGuia(guia, idGuia, guiasAgregadas) {
  // // console.log(idGuia);
  // console.log(guia);
  let listTemp = guiasAgregadas.map((guiaCreada) => {
    if (guiaCreada.idGuia === idGuia) {
      return guia;
    } else {
      return guiaCreada;
    }
  });
  // console.log(listTemp);
  return (dispach) => {
    dispach({
      type: UPDATE_GUIA,
      payload: listTemp,
    });
  };
}
