export const findGuia = (tipo, num, errorSms, noEncontrada) => {
  return async (dispatch) => {
    dispatch({
      type: GET_GUIAS,
    });
    try {
      let listTempGuias = [];
      let query = searchQuery(tipo);
      if (query === "idGuia") {
        console.log("entro aqui");
        let resp = await db
          .collection("guias")
          .doc(`${num}`)
          .get()
          .then((doc) => {
            console.log(doc);
            return doc.data();
          });
        if (resp === undefined) {
          noEncontrada();
        } else {
          listTempGuias = [...listTempGuias, resp];
        }
      } else {
        console.log("----------nueva query--------");
        console.log(query);
        let resp = await db
          .collection("guias")
          .where(query, "==", num)
          .get()
          .then((doc) => {
            let guides = [];
            doc.docs.forEach((element) => {
              let result = element.data();
              result.id = element.id;
              guides.push(result);
            });
            return guides;
          });
        if (resp?.length === 0) {
          console.log(resp);
          noEncontrada();
        } else {
          listTempGuias = resp;
        }
      }

      dispatch({
        type: GET_GUIAS_SUCCESS,
        payload: listTempGuias,
      });
    } catch (error) {
      console.log(error);
      dispatch({
        type: GET_GUIAS_ERROR,
        payload: "Error al buscar guías",
      });
      errorSms();

      setTimeout(() => {
        dispatch({
          type: GET_GUIAS_ERROR,
          payload: null,
        });
      }, 6000);
    }
  };
};
export const findLastIDGuia = (success, errorsm) => {
  return async (dispatch) => {
    dispatch({
      type: GET_ID_GUIA,
    });
    try {
      const resp = await db
        .collection("num_guia")
        .doc("1r5LfuzXicxnsN1jj9s8")
        .get()
        .then((doc) => {
          return doc.data();
        });

      dispatch({
        type: GET_ID_GUIA_SUCCESS,
        payload: resp,
      });
      success();
    } catch (error) {
      errorsm();
      dispatch({
        type: GET_ID_GUIA_ERROR,
        payload: `${error}`,
      });

      setTimeout(() => {
        dispatch({
          type: GET_ID_GUIA_ERROR,
          payload: null,
        });
      }, 6000);
    }
  };
};

