export const guardarGuias = (
  guias,
  mostrarAlertaOk,
  mostrarAlertaError,
  printguia
) => {
  return async (dispatch) => {
    dispatch({
      type: INSERT_GUIA,
    });

    try {
      let tempListPrint = [];
      let resp = {};
      resp = await db
        .collection("num_guia")
        .doc("1r5LfuzXicxnsN1jj9s8")
        .get()
        .then((doc) => {
          return doc.data();
        });
      guias.forEach(async (guia) => {
        let newGuia = generateGuiaNumber(parseFloat(resp?.num_guia) + 1);
        resp = {
          num_guia: parseFloat(resp?.num_guia) + 1,
        };
        // guia = {...guia, idGuia: newGuia };
        guia.idGuia = newGuia;
        tempListPrint = [...tempListPrint, guia];
        await db.collection("guias").doc(`${guia.idGuia}`).set(guia);
        await db
          .collection("num_guia")
          .doc("1r5LfuzXicxnsN1jj9s8")
          .update(resp);
      });
      salveToPrint(tempListPrint);
      dispatch({
        type: SHOW_TO_SALVE,
        payload: true,
      });
      printguia();
      dispatch({
        type: SHOW_TO_SALVE,
        payload: false,
      });
      setTimeout(() => {
        dispatch({
          type: RESETEAR_ADDGUIAS_CURRENTGUIA,
        });
        mostrarAlertaOk();
      }, 3000);
    } catch (error) {
      console.log("===> Error al guardar guías");
      console.log(error);
      dispatch({
        type: INSERT_GUIA_ERROR,
      });
      mostrarAlertaError();
    }
  };
};
export const salveToPrint = (ListGuias) => {
  return (dispatch) => {
    dispatch({
      type: SET_PRINT_GUIAS,
      payload: [],
    });
    dispatch({
      type: SET_PRINT_GUIAS,
      payload: ListGuias,
    });
  };
};
