import "../btn-service/btnService.css"

function BtnService({ title, description, classN, handleChangeStyle, index }) {

    return (

        <div className={`${classN} col btns-info m-2`} onClickCapture={() => handleChangeStyle(index)}>
            <div className='row justify-content-center m-2'>
                <div className='col-xs-12 col-md-12 item-card'>
                    <h3>{title}</h3>
                    <p>{description}</p>
                </div>
            </div>
        </div>

    )
}

export default BtnService
