import React, { useState } from 'react'
import "../Agendar-cita/agendarCita.css"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faUser, faAt, faPhone, faCity, faCheckCircle } from "@fortawesome/free-solid-svg-icons"
import clienteAxios from '../../../config/axios';
import Modal from "react-modal";

Modal.setAppElement("#root");
const customStyles = {
    content: {
        top: "50%",
        left: "50%",
        right: "auto",
        bottom: "auto",
        marginRight: "-50%",
        transform: "translate(-50%, -50%)",
        width: "25%",
    },
};

function AgendarCita() {
    const [nombre, setNombre] = useState('');
    const [correo, setCorreo] = useState('');
    const [telefono, setTelefono] = useState('');
    const [razon, setRazon] = useState('');
    const [mensaje, setMensaje] = useState('');
    const [error, setError] = useState('');
    const [modalIsOpen, setIsOpen] = useState(false);

    const openModal = () => {
        setIsOpen(true);
    };

    const closeModal = () => {
        setIsOpen(false);
    };

    const sendEmail = async () => {
        // console.log("ENVIANDO CORREO...");
        try {
            await clienteAxios.post('/api/mail/sendemailcita', {
                nombre,
                correo,
                telefono,
                razon,
                mensaje,
            });
            // console.log(resp);
            setError('');
            // Mostrar Mensaje de ok
            openModal();
            setCorreo('');
            setNombre('');
            setMensaje('');
            setRazon('');
            setTelefono('');
        } catch (error) {
            // console.log("Error en SendEmail");
            // console.log(error);
            setError("No se pudo Enviar el Correo. Intentalo más tarde.");
        }
    }

    const verifyForm = () => {
        const emailRegex = /^[-\w.%+]{1,64}@(?:[A-Z0-9-]{1,63}\.){1,125}[A-Z]{2,63}$/i;
        // console.log(emailRegex.test(correo));
        const isEmail = emailRegex.test(correo);

        if (!isEmail) {
            setError('El correo no es válido.')
        }
        if (nombre === '') {
            setError('El nombre es obligatorio.')
        }
        if (telefono === '') {
            setError('El teléfono es obligatorio.')
        }
        if (razon === '') {
            setError('La razón es obligatoria.');
        }
        if (mensaje === '') {
            setError('El mensaje es obligatorio.')
        }
        if (nombre.trim() !== '' && correo !== '' && isEmail && telefono.trim() !== '' && razon.trim() !== '' && mensaje.trim() !== '') {
            // console.log(isEmail);
            sendEmail();
        }
    }
    return (
        <>
            <div className="col container-Agendar" >
                <h5 id='sub-title'>Contáctanos</h5>
                <h4 >Envíanos tu Mensaje</h4>
                <div className="container-rowF">
                    <div>
                        <FontAwesomeIcon className="icon" icon={faUser} />
                        <input className='form-control' value={nombre} onChange={(e) => setNombre(e.target.value)} type="text" name="name" id="name" placeholder="Nombres Completos" />
                    </div>

                    <div>
                        <FontAwesomeIcon className="icon" icon={faAt} />
                        <input className='form-control' type="email" value={correo} onChange={(e) => setCorreo(e.target.value)} name="email" id="email" placeholder="Correo" />
                    </div>
                    <div>
                        <FontAwesomeIcon className="icon" icon={faPhone} />
                        <input className='form-control' type="text" name="phone" value={telefono} onChange={(e) => setTelefono(e.target.value)} id="phone" placeholder="Teléfono" />
                    </div>
                    <div>
                        <FontAwesomeIcon className="icon" icon={faCity} />
                        <input className='form-control' type="text" name="razon" id="razon" value={razon} onChange={(e) => setRazon(e.target.value)} placeholder="Razón Social" />
                    </div>
                    <textarea className='form-control' name="mensaje" id="mensaje" cols="30" value={mensaje} onChange={(e) => setMensaje(e.target.value)} rows="10" placeholder="Mensaje*"></textarea>
                </div>
                {
                    error !== '' ?
                        <p className="error">{error}</p>
                        : <></>
                }
                <div className="btn-line-send" type="button" onClickCapture={() => {
                    verifyForm();
                }}>
                    Enviar
            </div>
            </div>
            <Modal
                isOpen={modalIsOpen}
                onRequestClose={closeModal}
                style={customStyles}
                contentLabel="Mensaje"
            >
                <div className="container">
                    <div className="w-100 d-flex justify-content-center align-items-center">
                        <FontAwesomeIcon icon={faCheckCircle} className="h2 text-success text-center" />
                    </div>
                    <h5 className="text-success">Correo Envíado! Espera una respuesta</h5>

                </div>
            </Modal>
        </>
    )
}

export default AgendarCita
