export let guias_reducer = (state = initialState, action) => {
  switch (action.type) {
    case ADD_GUIA:
      return {
        ...state,
        guiasAgregadas: [...state.guiasAgregadas, action.payload],
      };

    case DELETE_GUIA:
      return {
        ...state,
        guiasAgregadas: state.guiasAgregadas.filter(
          (guia) => guia.idGuia !== action.payload
        ),
        currentEditGuia: initialState.currentEditGuia,
      };
    case SHOW_TO_SALVE:
      return {
        ...state,
        showtoSalve: action.payload,
      }
    case RESETEAR_ADDGUIAS_CURRENTGUIA:
      return {
        ...state,
        guiasAgregadas: [],
        currentEditGuia: initialState.currentEditGuia,
        fetching: false,
      };
    case UPDATE_GUIA:
      return {
        ...state,
        currentEditGuia: initialState.currentEditGuia,
        guiasAgregadas: action.payload,
      };
    //BOrrar datos
    case LIMPIAR_GUIA:
      return {
        ...state,
        currentEditGuia: initialState.currentEditGuia,
        currentDetailGuia: initialState.currentDetailGuia,
      };
    //rellenar inputs de guia
    case CHANGE_CURRENT_GUIA:
      return {
        ...state,
        currentEditGuia: action.payload,
      };
    
    case INSERT_GUIA:
      return {
        ...state,
        fetching: true,
        showtoSalve: true,
      };
    case UPDATE_STATUS_GUIA:
      return {
        ...state,
        fetching: true,
      };

    case UPDATE_STATUS_GUIA_ONLIST:
      return {
        ...state,
        listGuias: action.payload,
      };
    case SET_PRINT_GUIAS:
      return {
        ...state,
        ListPrintGuias: action.payload,
      };
    case UPDATE_STATUS_GUIA_SUCCESS:
      return {
        ...state,
        fetching: false,
        msgError: null,
      };
    case UPDATE_STATUS_GUIA_ERROR:
      return {
        ...state,
        fetching: false,
        msgError: action.payload,
      };
    case INSERT_GUIA_SUCCESS:
      return {
        ...state,
        fetching: false,
        msgError: null,
      };
    case INSERT_GUIA_ERROR:
      return {
        ...state,
        fetching: false,
        msgError: action.payload,
      };
    case PRINT_GUIAS_SEARCH:
      return {
        ...state,
        printGuia : action.payload,
      };
    case GET_GUIAS_ANULADAS:
      return {
        ...state,
        msgError: null,
        listGuiasAnuladas: action.payload,
      };
    case UPDATE_LIST_GUIA_ANULADA:
      return {
        ...state,
        listGuiasAnuladas: action.payload,
      };
    case GET_GUIAS:
      return {
        ...state,
        fetching: true,
        msgError: null,
      };
    case GET_GUIAS_SUCCESS:
      return {
        ...state,
        fetching: false,
        msgError: null,
        listGuias: action.payload,
      };
    case GET_GUIAS_ERROR:
      return {
        ...state,
        fetching: false,
        msgError: action.payload,
      };

    case GET_ID_GUIA:
      return {
        ...state,
        fetching: true,
        msgError: null,
      };
    case GET_ID_GUIA_SUCCESS:
      return {
        ...state,
        fetching: false,
        msgError: null,
        lastIdGuia: action.payload,
      };
    case GET_ID_GUIA_ERROR:
      return {
        ...state,
        fetching: false,
        msgError: action.payload,
      };
    case CURRENT_DETAIL_GUIA:
      return {
        ...state,
        currentDetailGuia: action.payload,
      };
    // Actualizar pesos
    case ADD_GUIAS_ACTUALIZAR_PESO:
      return {
        ...state,
        ListGuiasPesoActualizado: [
          ...state.ListGuiasPesoActualizado,
          action.payload,
        ],
        ListGuiasParaPeso: {},
      };
    case UPDATE_PESO_EDIT_ACTUALIZADOS:
      return {
        ...state,
        ListGuiasPesoActualizado: action.payload,
      };
    case DELETE_GUIA_PESO_ACTUALIZADO:
      return {
        ...state,
        ListGuiasPesoActualizado: action.payload,
      };
    case CURRENT_DETAIL_GUIA_PESOS:
      return {
        ...state,
        currentDetailGuiaPesos: action.payload,
      };
    case GET_GUIAS_ACTUALIZAR_PESO:
      return {
        ...state,
        fetching: true,
        msgError: null,
      };
    case GET_GUIAS_ACTUALIZAR_SUCCESS:
      return {
        ...state,
        fetching: false,
        msgError: null,
        ListGuiasParaPeso: action.payload,
      };
    case GET_GUIAS_ACTUALIZAR_ERROR:
      return {
        ...state,
        fetching: false,
        msgError: action.payload,
      };
    case LIMPIAR_LISTA_ATUALIZAR_PESO_SEARCH:
      return {
        ...state,
        ListGuiasParaPeso: {},
      };
    case UPDATE_PESOS_GUIAS_FIREBASE:
      return {
        ...state,
        fetching: false,
        msgError: null,
        // ListGuiasPesoActualizado: [...state.ListGuiasPesoActualizado, action.payload],
      };
    case UPDATE_PESOS_GUIAS_FIREBASE_SUCCESS:
      return {
        ...state,
        fetching: false,
        msgError: null,
        listGuias: [],
        listGuiasAnuladas: [],
        ListGuiasParaPeso: {},
        ListGuiasPesoActualizado: [],
      };
    case UPDATE_PESOS_GUIAS_FIREBASE_ERROR:
      return {
        ...state,
        fetching: false,
        msgError: action.payload,
      };
    default:
      return state;
  }
};
