import React from 'react';
import { useSelector } from 'react-redux';
import { useFormCreateUser } from '../../../../hooks/use_form_crearUsuario';
import '../crear_usuarios/crear_usuarios.css';

function CrearUsuarios() {
	const {
		auth: { fetching }
	} = useSelector((state) => state);

	const [formValues, handleInputChange, handlerBlur, onClickChangeHandler, handlerBlurEmail] = useFormCreateUser({
		rol: "coorporativo",
		nombre: "",
		email: "",
		codigoPostal: "",
		dni: "",
		telefPrincipal: "",
		direccion: "",
		telef1: "",
		provincia: "Loja",
		telef2: "",
		canton: "Loja",
		parroquia: "",
		password: "",
		repassword: "",
		errorMsg: "",
		errorEmail: ""
	})

	const { errorMsg, repassword, password, rol, nombre, email, codigoPostal, dni, telefPrincipal, direccion, telef1, provincia, telef2, canton, parroquia, errorEmail } = formValues;

	return (
		<div className="crearGuias col-md-9 ms-sm-auto col-lg-10 px-md-4">
			<div className="container-fluid pt-2">
				<h4 className="title-separtor">Crear Usuario</h4>
				<hr />
				<div className="row align-items-center">
					<div className="col-sm-12 col-md-4 align-self-start">
						<h4 className="title-separtor pt-2">Datos de Usuario</h4>
					</div>
					<div className="col-sm-12 col-md-2 align-self-start">
						<h4 className="title-separtor pt-2">Tipo de Usuario: </h4>
					</div>
					<div className="col-sm-12 col-md-6">
						<select
							className="col  align-self-start form-select"

							placeholder="Tipo Usuario"
							name="rol"
							value={rol}
							onChange={handleInputChange}
						>
							<option value="admin">Administrador</option>
							<option value="puntoVenta" >Puntos de Venta</option>
							<option value="sucursal">Sucursales</option>
							<option value="coorporativo">Clientes Coorporativos</option>
						</select>
					</div>
				</div>
				<hr />
				<div className="card card-body">
					{/* <div className='no-Item'>
						<p className="text-muted noGuias">No hay guia seleccionada</p>
					</div> */}
					<h5 className="mt-3 titleSection-form">Datos de Usuario</h5>
					<hr />
					<div className="row ">
						<div className="col-lg-6 ">
							<form>
								<div className="mb-3">
									<label htmlFor="simpleinput" className="form-label-input">
										Nombre Completo
									</label>
									<input type="text" className="form-control" placeholder="Nombres"
										name="nombre" value={nombre} onChange={handleInputChange} />
								</div>

								<div className="mb-3">
									<label htmlFor="simpleinput" className="form-label-input">
										Ruc/Dni
									</label>
									<input
										type="text"

										className="form-control"
										placeholder="110XXXXXXXXX"
										name="dni"
										value={dni}
										onChange={handleInputChange}
									/>
								</div>
								<div className="mb-3">
									<label htmlFor="simpleinput" className="form-label-input">
										Dirección
									</label>
									<input
										type="text"

										className="form-control"
										placeholder="Praza Ramón, 31, 8º F"
										name="direccion"
										value={direccion}
										onChange={handleInputChange}
									/>
								</div>
								<div className="mb-3">
									<label   className="form-label-input">
										Provincia
									</label>
									<select className="form-select" name="provincia" value={provincia} onChange={handleInputChange}>
										<option value="loja">Loja</option>
										<option value="zamora">Zamora</option>
										<option value="cuenca">Cuenca</option>
										<option value="imbabura">Imbabura</option>
										<option value="los rios">Los Rios</option>
									</select>
								</div>
								<div className="mb-3">
									<label   className="form-label-input">
										Canton
									</label>
									<select className="form-select" name="canton" value={canton} onChange={handleInputChange}>
										<option value="loja">Loja</option>
										<option value="zamora">Zamora</option>
										<option value="cuenca">Cuenca</option>
										<option value="imbabura">Imbabura</option>
										<option value="los rios">Los Rios</option>
									</select>
								</div>
								<div className="mb-3">
									<label htmlFor="simpleinput" className="form-label-input" >
										Parroquia
									</label>
									<input
										type="text"

										className="form-control"
										placeholder="Praza Ramón"
										name="parroquia"
										value={parroquia}
										onChange={handleInputChange}
									/>
								</div>
							</form>
						</div>

						<div className="col-lg-6 ">
							<form>
								<div className="mb-3">
									<label htmlFor="simpleinput" className="form-label-input">
										Email
									</label>
									<input
										type="email"
										className="form-control"
										id="inputEmail"
										placeholder="example@example.com"
										name="email"
										value={email}
										onChange={handleInputChange}
										onBlurCapture={handlerBlurEmail}
									/>
									{
										errorEmail !== "" ?
											<div className="container-error">
												<p className="text-error">{errorEmail}</p>
											</div>
											: <></>

									}
								</div>
								<div className="mb-3">
									<label htmlFor="simpleinput" className="form-label-input">
										Código Postal
									</label>
									<input type="text" className="form-control" placeholder="Código Postal..."
										name="codigoPostal" value={codigoPostal} onChange={handleInputChange} />
								</div>

								<div className="mb-3">
									<label htmlFor="simpleinput" className="form-label-input">
										Télefono Principal
									</label>
									<input
										type="text"

										className="form-control"
										placeholder="Teléfono Principal"
										name="telefPrincipal"
										value={telefPrincipal}
										onChange={handleInputChange}
									/>
								</div>
								<div className="mb-3">
									<label htmlFor="simpleinput" className="form-label-input">
										Télefono 1
									</label>
									<input
										type="text"

										className="form-control"
										placeholder="Teléfono 1"
										value={telef1}
										name="telef1"
										onChange={handleInputChange}
									/>
								</div>
								<div className="mb-3">
									<label htmlFor="simpleinput" className="form-label-input">
										Télefono 2
									</label>
									<input
										type="text"
										className="form-control"
										placeholder="Apellidos Completos"
										name="telef2"
										value={telef2}
										onChange={handleInputChange}
									/>
								</div>
								<div className="mb-3">
									<label htmlFor="simpleinput" className="form-label-input">
										Contraseña
									</label>
									<input
										type="password"
										className="form-control"
										placeholder="Contrseña del usuario"
										name="password"
										value={password}
										onChange={handleInputChange}
									/>
								</div>
								<div className="mb-3">
									<label htmlFor="simpleinput" className="form-label-input">
										Confirmación de Contraseña
									</label>
									<input
										type="password"
										className="form-control"
										placeholder="Repite la Contrseña"
										name="repassword"
										value={repassword}
										onChange={handleInputChange}
										onBlur={handlerBlur}
									/>
								</div>
								{
									errorMsg !== "" ?
										<div className="container-error">
											<p className="text-error">{errorMsg}</p>
										</div>
										: <></>
								}
							</form>
						</div>
					</div>
				</div>

				<div className="card card-body">
					<div className="row">
						<button type="button" className=" btn-dt-del col mx-1 p-2 bt-a">
							Cancelar
						</button>
						<button type="button" className=" btn-dt-details col mx-1 bt-a" onClickCapture={() => {
							if (!fetching) {
								onClickChangeHandler()
							}
						}}>
							{
								fetching ?
									"Creando usuario..."
									: "Crear Usuario"
							}
						</button>
					</div>
				</div>
			</div>
		</div>
	);
}

export default CrearUsuarios;
