import { Redirect, Route } from 'react-router'
// import { IRouter } from '../interfaces/router.interface';

export const PrivateRoute = ({
    isAutenticated,
    component: Component,
    path,
    ...rest
}) => {
    return (
        <Route  {...rest}
            component={(props) => (
                (isAutenticated) ? <Component  {...props} /> : <Redirect to="/login" />
            )}
        />
    )
}