import {
  GET_GUIA_MANIFIESTO,
  GET_GUIA_MANIFIESTO_ERROR,
  GET_GUIA_MANIFIESTO_SUCCESS,
  ADD_GUIAS_MANIFIESTOS,
  CLEAN_LIST_MANIFIESTOS,
  DELETE_GUIA_SEARCH_SELECTED,
  DELETE_GUIA_MANIFIESTO,
  CREATE_MANIFIESTO_DB,
  CREATE_MANIFIESTO_DB_ERROR,
  CREATE_MANIFIESTO_DB_SUCCESS,
  GET_MANIFIESTO_DB,
  GET_MANIFIESTO_DB_ERROR,
  GET_MANIFIESTO_DB_SUCCESS,
  UPDATE_STATUS_GUIA_MANIFIESTO,
  SET_DESTINO_MANIFIESTO,
  ADD_ID_GUIAS_MANIFIESTOS,
  DELETE_ID_GUIAS_MANIFIESTOS,
  ADD_ID_GUIAS_MANIFIESTOS_RESEPCION,
  DELETE_ID_GUIAS_MANIFIESTOS_RESEPCION,
  SET_SEARCH_IDGUIA,
  SET_COMPROBACION_ENVIOS_RECINIDOS,
  SET_COMPROBACION_ENVIOS_MAL_ENCAMINADOS,
  SET_COMPROBACION_ENVIOS_PENDIENTES,
  UPDATE_MANIFIESTO_DB,
  UPDATE_MANIFIESTO_DB_ERROR,
  UPDATE_MANIFIESTO_DB_SUCCESS,
  NAME_CARTERO_ASIGNACION,
  GET_GUIA_ASIGNACION_DB,
  GET_GUIA_ASIGNACION_DB_ERROR,
  GET_GUIA_ASIGNACION_DB_SUCCESS,
  DELETE_GUIA_ASIGNACION_LIST,
  SET_ASIGNACION_DB,
  SET_ASIGNACION_DB_ERROR,
  SET_ASIGNACION_DB_SUCCESS,
} from "../../types/types";

const initialState = {
  idManifiesto: "",
  fetching: false,
  msgError: null,
  estado: "creado",
  credoPor: "Sucursal",
  fechaCreacion: Date.now(),
  fechaRecepcion: "",
  asignacion: {
    nomAsignado: "",
    fechaAsignado: "",
  },
  numEnvios: 0,
  numRecibidos: 0,
  numMalEncaminados: 0,
  numPendientes: 0,
  provinciaDestino: "",
  nameAsigned: "",
  searchIdReseptarGuia: "",
  listGuiasManifiesto: [],
  guiaSelectedSearch: {},
  currentManifiesto: {},
  idGuiasManifiesto: [],
  idGuiasRecepcionManifiesto: [],
  listGuiasAsignacion: [],
};

export let manifiesto_reducer = (state = initialState, action) => {
  switch (action.type) {
    case GET_GUIA_MANIFIESTO:
      return {
        ...state,
        fetching: true,
        msgError: null,
      };

    case GET_GUIA_MANIFIESTO_SUCCESS:
      return {
        ...state,
        fetching: false,
        msgError: null,
        guiaSelectedSearch: action.payload,
      };

    case GET_GUIA_MANIFIESTO_ERROR:
      return {
        ...state,
        fetching: false,
        msgError: action.payload,
      };

    case DELETE_GUIA_MANIFIESTO:
      return {
        ...state,
        listGuiasManifiesto: state.listGuiasManifiesto.filter(
          (guia) => guia.idGuia !== action.payload
        ),
      };
    case ADD_GUIAS_MANIFIESTOS:
      // console.log('entro al redicer add');
      return {
        ...state,
        listGuiasManifiesto: [
          ...state.listGuiasManifiesto,
          state.guiaSelectedSearch,
        ],
        guiaSelectedSearch: {},
      };

    case DELETE_GUIA_SEARCH_SELECTED:
      return {
        ...state,
        guiaSelectedSearch: {},
      };
    case CREATE_MANIFIESTO_DB:
      return {
        ...state,
        fetching: true,
      };
    case CREATE_MANIFIESTO_DB_ERROR:
      return {
        ...state,
        fetching: false,
        msgError: null,
      };
    case CREATE_MANIFIESTO_DB_ERROR:
      return {
        ...state,
        fetching: false,
        msgError: action.payload,
      };
    case CREATE_MANIFIESTO_DB_SUCCESS:
      return {
        ...state,
        fetching: false,
        msgError: null,
        listGuiasManifiesto: [],
        idGuiasManifiesto: [],
        guiaSelectedSearch: {},
        provinciaDestino: "",
      };
    case GET_MANIFIESTO_DB:
      return {
        ...state,
        fetching: true,
        msgError: null,
      };
    case GET_MANIFIESTO_DB_ERROR:
      return {
        ...state,
        fetching: false,
        msgError: null,
      };

    case GET_MANIFIESTO_DB_SUCCESS:
      return {
        ...state,
        fetching: false,
        msgError: action.payload,
        currentManifiesto: action.payload,
      };

    case UPDATE_STATUS_GUIA_MANIFIESTO:
      return {
        ...state,
        currentManifiesto: {
          ...state.currentManifiesto,
          listIdGuiasenEnviar: action.payload,
        },
        idGuiasRecepcionManifiesto: "",
      };
    case SET_DESTINO_MANIFIESTO:
      return {
        ...state,
        provinciaDestino: action.payload,
      };
    case ADD_ID_GUIAS_MANIFIESTOS:
      return {
        ...state,
        idGuiasManifiesto: [...state.idGuiasManifiesto, action.payload],
      };
    case DELETE_ID_GUIAS_MANIFIESTOS:
      return {
        ...state,
        idGuiasManifiesto: action.payload,
      };
    case ADD_ID_GUIAS_MANIFIESTOS_RESEPCION:
      return {
        ...state,
        idGuiasRecepcionManifiesto: [
          ...state.idGuiasRecepcionManifiesto,
          action.payload,
        ],
      };
    case SET_SEARCH_IDGUIA:
      return {
        ...state,
        idGuiasRecepcionManifiesto: action.payload,
      };
    case SET_COMPROBACION_ENVIOS_RECINIDOS:
      return {
        ...state,
        numRecibidos: action.payload,
      };
    case SET_COMPROBACION_ENVIOS_MAL_ENCAMINADOS:
      return {
        ...state,
        numMalEncaminados: action.payload,
      };
    case SET_COMPROBACION_ENVIOS_PENDIENTES:
      return {
        ...state,
        numPendientes: action.payload,
      };

    case UPDATE_MANIFIESTO_DB:
      return {
        ...state,
        fetching: true,
        msgError: null,
      };
    case UPDATE_MANIFIESTO_DB_ERROR:
      return {
        ...state,
        fetching: false,
        msgError: null,
      };

    case UPDATE_MANIFIESTO_DB_SUCCESS:
      return {
        ...state,
        fetching: false,
        msgError: action.payload,
        numEnvios: 0,
        numRecibidos: 0,
        numMalEncaminados: 0,
        numPendientes: 0,
        provinciaDestino: "",
        searchIdReseptarGuia: "",
        listGuiasManifiesto: [],
        guiaSelectedSearch: {},
        currentManifiesto: {},
        idGuiasManifiesto: [],
        idGuiasRecepcionManifiesto: [],
      };

    case NAME_CARTERO_ASIGNACION:
      return {
        ...state,
        nameAsigned: action.payload,
      };
    case GET_GUIA_ASIGNACION_DB:
      return {
        ...state,
        fetching: true,
        msgError: null,
      };
    case GET_GUIA_ASIGNACION_DB_SUCCESS:
      return {
        ...state,
        fetching: false,
        listGuiasAsignacion: [...state.listGuiasAsignacion, action.payload],
      };
    case GET_GUIA_ASIGNACION_DB_ERROR:
      return {
        ...state,
        fetching: false,
        msgError: action.payload,
      };
    case DELETE_GUIA_ASIGNACION_LIST:
      return {
        ...state,
        listGuiasAsignacion: action.payload,
      };
    case SET_ASIGNACION_DB:
      return {
        ...state,
        fetching: true,
        msgError: null,
      };
    case SET_ASIGNACION_DB_SUCCESS:
      return {
        ...state,
        fetching: false,
        listGuiasAsignacion: [],
        nameAsigned: "",
      };
    case SET_ASIGNACION_DB_ERROR:
      return {
        ...state,
        fetching: false,
        msgError: action.payload,
      };

    default:
      return state;
  }
};
