import { useToasts } from "react-toast-notifications";
import {
  deleteGuiaAsignacion,
  fingGuiaAsignacion,
  setAsignacionCorrespondencia,
  setNamedAsigned,
} from "../redux/actions/manifiestos_actions";
import { useDispatch, useSelector } from "react-redux";
import { useState, useEffect } from "react";
import { v1 as uuid } from "uuid";

function AsignacionManifiesto(initialState = "") {
  const dispatch = useDispatch();
  const { addToast } = useToasts();
  const [nameCartero, setnameCartero] = useState(initialState);

  const {
    manifiestos: { listGuiasAsignacion, fetching, msgError, nameAsigned },
  } = useSelector((state) => state);

  useEffect(() => {
    if (nameAsigned && nameAsigned !== "") {
      setnameCartero(nameAsigned);
    }
  }, [nameAsigned]);

  const updateCartero = (e) => {
    e.preventDefault();
    dispatch(setNamedAsigned(e.target.value));
    setnameCartero(e.target.value);
  };

  const toastSms = (text, type) => {
    return addToast(text, {
      appearance: type,
      autoDismiss: true,
      autoDismissTimeout: 3000,
    });
  };

  const handlerSearchGuia = (e) => {
    e.preventDefault();
    const success = () => toastSms("Guia encontrada", "success");
    const error = () => toastSms(`${msgError}`, "success");
    let guiasEnLista = listGuiasAsignacion.filter((item) =>
      item.idGuia.includes(e.target.value)
    ).length;

    if (guiasEnLista === 1) {
      toastSms("Esta guia ya fue agregada ", "info");
    } else {
      if (nameCartero === "") {
        toastSms("debe seleccionar un encargado antes de agregar", "info");
      } else {
        if (e.target.value.trim()) {
          dispatch(fingGuiaAsignacion(e.target.value, error, success));
        } else {
          toastSms("El campo a buscar esta vacio", "info");
        }
      }
    }
  };
  const deletefromfromList = (guia) => {
    let listtemp = listGuiasAsignacion.filter(
      (guiaitem) => guiaitem.idGuia !== guia.idGuia
    );

    dispatch(deleteGuiaAsignacion(listtemp));
    toastSms("Guia eliminada de la lista", "info");
  };
  const setAsignaciondb = () => {
    const success = () => toastSms("Asignacion creada correctamente", "success");
    const error = () => toastSms(`${msgError}`, "success");
    let tempIDGuias = listGuiasAsignacion.map((guia) => guia.idGuia);
    // console.log(tempIDGuias);
    const tempAsignacion = {
      idAsignacion: uuid(),
      fechaAsignacion: Date.now().toString(),
      personaAsignada: nameCartero,
      guiasAsignadas: tempIDGuias,
    };

    dispatch(
      setAsignacionCorrespondencia(tempAsignacion, tempIDGuias, error, success)
    );
  };
  return [
    nameCartero,
    listGuiasAsignacion,
    fetching,
    setAsignaciondb,
    deletefromfromList,
    handlerSearchGuia,
    setnameCartero,
    updateCartero,
  ];
}

export default AsignacionManifiesto;
