import React from 'react'
import "../btn-service-responsive/btnServiceResp.css"

function BtnServiceResp({ title, classN, handleChangeStyle, index }) {

    return (

        <div className={`${classN} col item-menu`} onClickCapture={() => handleChangeStyle(index)}>
            <h1 className='title'>{title}</h1>
        </div>

    )
}

export default BtnServiceResp
