import React from 'react';

const ComponentRuta = ({ handleChangeCiudadDestino, coverturasList, mostrarProvincia, covertura }) => {

    return (
        <>
            <div className="col-md-6">
                <div className="form-group first">
                    <label className="laber-input" htmlFor="fname">Ciudad de Origen</label>
                    <input type="text" className="form-control input-content" placeholder="Loja" id="fname" disabled readOnly />
                </div>
            </div>
            <div className="col-md-6">
                <div className="form-group first">
                    <label className="laber-input" htmlFor="fname">Ciudad de Destino</label>
                    <select
                        className="form-control input-content"
                        name="ciudad"
                        onChange={handleChangeCiudadDestino}
                    >
                        <option value="0">Seleccionar...</option>
                        {coverturasList.map((covertura, index) => {
                            return (
                                <>
                                    {(
                                        // covertura.Destino.map((ciudad, index) => {
                                        //   return (
                                        <option key={index} value={covertura.Destino}>
                                            {covertura.Destino}
                                        </option>
                                        //   );
                                        // })
                                    )}
                                </>
                            );
                        })}
                    </select>
                </div>
            </div>
            {mostrarProvincia ?
                <div className="col-12">
                    <div className="form-group first">
                        <label className="laber-input" htmlFor="fname">Provincia</label>
                        <input type="text" value={covertura.Provincia} className="form-control input-content" placeholder={covertura.Provincia} id="fname" disabled readOnly />
                    </div>
                </div>
                : <></>
            }
        </>
    );
}

export default ComponentRuta;