import {
    LOGIN,
    LOGIN_ERROR,
    LOGIN_SUCCESS,
    REGISTER_USER,
    REGISTER_USER_ERROR,
    REGISTER_USER_SUCCESS,
    CERRAR_SESION,
    GET_ROL_FUNCTIONS,
    GET_ROL_FUNCTIONS_ERROR,
    GET_ROL_FUNCTIONS_SUCCESS
} from "../../types/types";

const initialState = {
    fetching: false,
    accessToken: localStorage.getItem("accessToken") || null,
    errorLogin: null,
    dataProfile: JSON.parse(localStorage.getItem("dataProfile")) || null,
    funciones: JSON.parse(localStorage.getItem("rfas_funciones")) || null,
    error: null
}

export const authReducer = (state = initialState, action) => {
    switch (action.type) {
        case LOGIN:
        case REGISTER_USER:
            return {
                ...state,
                fetching: true,
                errorLogin: null
            }
        case REGISTER_USER_SUCCESS:
            return {
                ...state,
                fetching: false
            }
        case REGISTER_USER_ERROR:
            return {
                ...state,
                fetching: false,
                errorLogin: action.payload
            }
        case LOGIN_SUCCESS:
            return {
                ...state,
                fetching: false,
                accessToken: action.payload.accessToken,
                dataProfile: action.payload.dataProfile,
                errorLogin: null
            }
        case LOGIN_ERROR:
            return {
                ...state,
                fetching: false,
                accessToken: null,
                dataProfile: null,
                errorLogin: action.payload
            }
        case CERRAR_SESION:
            return {
                ...state,
                fetching: false,
                accessToken: null,
                errorLogin: null,
                dataProfile: null,
            };
        case GET_ROL_FUNCTIONS:
            return state;
        case GET_ROL_FUNCTIONS_SUCCESS:
            return {
                ...state,
                funciones: action.payload
            }
        case GET_ROL_FUNCTIONS_ERROR:
            return {
                ...state,
                error: action.payload
            }
        default:
            return state;
    }
}