import {
    GET_LISTA_ROLES,
    GET_LISTA_ROLES_ERROR,
    GET_LISTA_ROLES_SUCCESS,
    UPDATE_ROL_FUNCTIONS,
    UPDATE_ROL_FUNCTIONS_ERROR,
    UPDATE_ROL_FUNCTIONS_SUCCESS
} from "../../types/types";

const initialState = {
    fetching: false,
    listRoles: null
}

export const rolesReducer = (state = initialState, action) => {
    switch (action.type) {
        case GET_LISTA_ROLES:
            return {
                ...state,
                fetching: true,
            }
        case GET_LISTA_ROLES_ERROR:
            return {
                ...state,
                fetching: false,
                listRoles: null,
            }
        case GET_LISTA_ROLES_SUCCESS:
            return {
                ...state,
                fetching: false,
                listRoles: action.payload
            }
        case UPDATE_ROL_FUNCTIONS:
            return {
                ...state,
                fetching: true
            }
        case UPDATE_ROL_FUNCTIONS_SUCCESS:
            return {
                ...state,
                fetching: false
            }
        case UPDATE_ROL_FUNCTIONS_ERROR:
            return {
                ...state,
                fetching: false
            }
        default:
            return state;
    }
}