import React, { useEffect } from 'react'
import "../empresa-page/empresa-page.css";
import ServiciosSection from "../servicios_section/Servicios_section";
import image from "../../img/image-courier.jpeg";
import image2 from "../../img/mision.png";
import image3 from "../../img/vision.png";
import Navbar from '../navbar/Navbar';
import ContactSection from '../contact-section/contact_section';
import Footer from '../footer/footer';
function EmpresaPage() {
  useEffect(() => {
    console.log("=======> DENTRO DE EMPRESA-PAGE.JS USEEFFECT")
    window.scrollTo(0, 0);
  }, []);
  return (
    <div className="nosotros">
      <Navbar
        shbtnLogin='True'
        shcontactos='True'
        shservicios='True'
      />
      <div className="nostros-text-container">
        <h1 className='nosotros-text'>Nosotros</h1>
      </div>
      <div className="bannertop">
        <h2 className='title-nosotros'><span className="right-top"></span>Nosotros<span className="right-top"></span></h2>
      </div>
      <div className="container">
        <section>
          <div className="row align-items-end">
            <div className="description-card col-xs-12 col-md-6 p-4">
              {/* <h4 id="sub-title"></h4> */}
              <h4 id="title">BIENVENIDA<span className="right"></span></h4>
              {/* <h3>¡ Un verdadero Compromiso !</h3> */}
              <p id="paraf1">
                Reciba cordiales saludos de éxito de la Empresa Rapifas Courier
                Cía. Ltda. Estamos dispuestos a brindar nuestro portafolio de
                servicios logísticos desarrollado por compañía, con el propósito
                de crear lazos fuertes comerciales y ser sus aliados
                estratégicos en la búsqueda de sus objetivos. Somos una Empresa
                Postal que proporciona a nuestros clientes la seguridad
                necesaria para el custodio y traslado de sus envíos a nivel
                local, provincial y nacional. Dentro del proceso de
                modernización en el que la empresa se encuentra constantemente,
                hemos diseñado diversas prestaciones tecnológicas para cumplir
                con la demanda de nuestros clientes y ofrecer una experiencia
                inmersa en profesionalismo y optimización.
              </p>
            </div>
            <div className="col-xs-12 col-md-6">
              <img className="background-img" src={image} alt="img-back" />
            </div>
          </div>
        </section>
        <section className="back-nosotros">

          <div className="row align-items-center data">
            <div className="col-xs-12 col-md-6">
              <img className="background-img" src={image3} alt="img-back" height="400" />
            </div>
            <div className="description-card col-xs-12 col-md-6 p-4">
              <h4 id="title">MISIÓN <span className="right"></span></h4>
              <p id="paraf1">
                Servir a nuestra ciudadanía, brindando soluciones logísticas que
                impulsen el sector comercial de nuestros clientes, sean personas
                naturales o jurídicas. Nuestro objetivo principal es ofertar los
                costos más bajos del mercado ofreciendo siempre un servicio de
                calidad, inmerso en tecnología e innovación constante, para ser
                tu aliado estratégico en búsqueda de todos sus objetivos.
              </p>
            </div>
          </div>
          <div className="row align-items-center data">
            <div className="description-card col-xs-12 col-md-6 p-4">
              <h4 id="title"> <span className="right"></span> VISIÓN</h4>
              <p id="paraf1">
                Convertirnos en la presente década, en la mejor alternativa
                Courier en toda la región, identificándonos como una empresa
                modelo justa, eficaz, tecnológica e innovadora que impulsa y
                fortalece la logística comercial de todos nuestros clientes a
                nivel nacional. Queremos ser el operador logístico más
                representativo del país.
              </p>
            </div>
            <div className="col-xs-12 col-md-6">
              <img className="background-img" src={image2} alt="img-back" />
            </div>
          </div>
        </section>
        <section>
          <div className='mt-4'>
            <ServiciosSection />
          </div>
        </section>
      </div>
      <ContactSection />
      <Footer />
    </div>
  );
}

export default EmpresaPage;
