import React from "react";
import "../anular-guias/anularGuias.css";
import { useDispatch, useSelector } from "react-redux";
import {
  actualizarEstadoGuia,
  currentGuiaDetail,
  limpiarGuia,
  // updateitemListDebaja,
  updateListAnuladas,
} from "../../../../redux/actions/guias_actions";
import { ParseDate } from "../../../../helpers/helper";
import Dashfooter from "../../../dash-componets/footter/dashfooter";

import Swal from "sweetalert2";

function AnularGuias() {
  const dispatch = useDispatch();
  const {
    guias: { listGuiasAnuladas, msgError, currentDetailGuia },
  } = useSelector((state) => state);

  const cleanValues = () => {
    // // console.log("Elimiar datso");
    dispatch(limpiarGuia());
  };

  const verDetaller = (guia) => {
    dispatch(currentGuiaDetail(guia));
  };
  const {
    remitente,
    destinatario,
    peso,
    contenido,
    // fechaActual,
    // idGuia,
    motivo,
    numPiezas,
    tipo,
  } = currentDetailGuia;

  //Alerts
  const mostrarAlertaOk = () => {
    return Swal.fire({
      title: "Actualizado con éxito",
      Icon: "success",
    });
  };
  const mostrarAlertaError = () => {
    return Swal.fire({
      title: "Error al Actualizado Guias!",
      Icon: "error",
    });
  };

  //Alerts fin

  const handlerAnularGuia = () => {
    // console.log("ejecuto la anulacion");
    let updateStadoGuia = {
      ...currentDetailGuia,
      estado: "anulada",
      fechaAnulacion: Date.now().toString(),
    };
    dispatch(
      actualizarEstadoGuia(updateStadoGuia, mostrarAlertaOk, mostrarAlertaError)
    );
    dispatch(
      updateListAnuladas(
        updateStadoGuia,
        updateStadoGuia.idGuia,
        listGuiasAnuladas
      )
    );
    cleanValues();
  };

  const showitemNoList = () => {
    // console.log('desde anuladas');
    
    let list = listGuiasAnuladas.map((e) => {
      if (e.estado === "pendiente anulacion") {
        return e;
      }
    });
    if (list.length >= 1) {
      // console.log(list.length);
      return true;

    } else {

      // console.log(list.length);
      return false;
    }
  };

  return (
    <div className="crearGuias col-md-9 ms-sm-auto col-lg-10 px-md-4">
      {msgError ? (
        <div className="container-errorCrear">
          <p>{msgError}</p>
        </div>
      ) : (
        <></>
      )}
      <div className="container-fluid pt-4">
        <h4 className="title-separtor">Anular Guias</h4>
        <hr />
        <div className="card card-body">
          <div className="row align-items-center">
            <div className="col-sm-12 col-md-4">
              <h5 className="title-separtor">Pendientes de Anulacion</h5>
            </div>
          </div>
          <hr />
          <div className="row align-items-start">
            <div className="row table-cm">
              <table className="table dt-responsive nowrap">
                <thead>
                  <tr className="header-table">
                    <th
                      className="truencate-id"
                      data-bs-toggle="tooltip"
                      data-bs-placement="bottom"
                      title="Num. Guia">ID</th>
                    <th
                      className="truencate-id"
                      data-bs-toggle="tooltip"
                      data-bs-placement="bottom"
                      title="Fecha"
                    >
                      Fecha
                    </th>
                    <th
                      className="truencate-id"
                      data-bs-toggle="tooltip"
                      data-bs-placement="bottom"
                      title="Ciudad"
                    >
                      Ciudad
                    </th>
                    <th
                      className="truencate-id"
                      data-bs-toggle="tooltip"
                      data-bs-placement="bottom"
                      title="Ciudad"
                    >
                      Ciudad
                    </th>
                    <th
                      className="truencate-id"
                      data-bs-toggle="tooltip"
                      data-bs-placement="bottom"
                      title="Morivo"
                    >
                      Morivo
                    </th>
                    <th
                      className="truencate-id"
                      data-bs-toggle="tooltip"
                      data-bs-placement="bottom"
                      title="Estado"
                    >
                      Estado
                    </th>
                    <th
                      className="truencate-id"
                      data-bs-toggle="tooltip"
                      data-bs-placement="bottom"
                      title="Detalles"
                    >
                      Detalles
                    </th>
                  </tr>
                </thead>
                <tbody>
                  {listGuiasAnuladas?.map((guia) =>
                    guia.estado === "pendiente anulacion" ? (
                      <tr key={guia.idGuia} className="hover-table">
                        <td className="truencate-id">{guia.idGuia}</td>
                        <td>{ParseDate(guia.fechaActual)}</td>
                        <td>
                          {`${guia.remitente?.provincia}-${guia.remitente?.canton}`}
                        </td>
                        <td>
                          {`${guia.destinatario?.provincia}-${guia.destinatario?.canton}`}
                        </td>
                        <td
                          className="truencate-id"
                          data-bs-toggle="tooltip"
                          data-bs-placement="bottom"
                          title={guia.motivo}
                        >
                          {guia.motivo}
                        </td>
                        <td
                          className="truencate-id"
                          data-bs-toggle="tooltip"
                          data-bs-placement="bottom"
                          title={guia.estado}
                        >
                          {guia.estado}
                        </td>

                        <td>
                          <div
                            type="button"
                            onClickCapture={() => verDetaller(guia)}
                            data-bs-toggle="tooltip"
                            data-bs-placement="bottom"
                            title="Ver Detalles"
                          >
                            <i className="material-icons">remove_red_eye</i>
                          </div>
                        </td>
                      </tr>
                    ) : (
                      <></>
                    )
                  )}
                </tbody>
              </table>
            </div>
          </div>
          <div>{showitemNoList ? <></> : <p>No hay items en la lista</p>}</div>
          <div>{listGuiasAnuladas?.length >= 1  ?   <></>: <p>No hay items en la lista</p> }</div>
        </div>
        {currentDetailGuia.remitente.nombres === "" ? (
          <></>
        ) : (
          <div className="form">
            <h4 className="title-separtor">Detalles</h4>
            <hr />
            <div className="card card-body">
              <div className="mb-3">
                <label className="form-label-input">Motivo Anulación</label>
                <input
                  readOnly
                  type="text"
                  className="form-control"
                  placeholder="Motivo de Anulación"
                  value={motivo === null ? "---" : motivo}
                  name="motivo"
                />
              </div>
              <div className="row ">
                <div className="col-lg-6">
                  <h5 className="titleSection-form">Remitente</h5>
                  <hr />
                  <div className="mb-3">
                    {/* <i className="material-icons">add</i> */}
                    <label className="form-label-input">Ruc/Dni</label>
                    <input
                      readOnly
                      type="text"
                      className="form-control"
                      placeholder="110XXXXXXXXX"
                      value={remitente?.ruc}
                      name="remitente ruc"
                    />
                  </div>
                  <div className="mb-3">
                    <label className="form-label-input">ID Remitente</label>
                    <input
                      readOnly
                      type="text"
                      className="form-control"
                      value={remitente?.idRemitente}
                      name="idRemitente"
                    />
                  </div>
                  <div className="mb-3">
                    <label className="form-label-input">
                      Nombres Completos
                    </label>
                    <input
                      readOnly
                      type="text"
                      className="form-control"
                      placeholder="Nombres Completos"
                      value={remitente?.nombres}
                      name="remitente nombres"
                      required
                    />
                  </div>
                  <div className="mb-3">
                    <label className="form-label-input">Teléfono</label>
                    <input
                      readOnly
                      type="text"
                      className="form-control"
                      placeholder="Teléfono Principal"
                      value={remitente?.telefono}
                      name="remitente telefono"
                    />
                  </div>
                  <div className="mb-3">
                    <label className="form-label-input">Provincia</label>
                    <input
                      readOnly
                      type="text"
                      className="form-control"
                      placeholder="Loja"
                      value={remitente?.provincia}
                      name="remitente provincia"
                      required
                    />
                  </div>
                  <div className="mb-3">
                    <label className="form-label-input">Cantón</label>
                    <input
                      readOnly
                      type="text"
                      className="form-control"
                      placeholder="Loja"
                      value={remitente?.canton}
                      name="remitente canton"
                    />
                  </div>
                  <div className="mb-3">
                    <label className="form-label-input">Parroquia</label>
                    <input
                      readOnly
                      type="text"
                      className="form-control"
                      placeholder="Loja"
                      value={remitente?.parroquias}
                      name="remitente canton"
                    />
                  </div>
                  <div className="mb-3">
                    <label className="form-label-input">Dirección</label>
                    <input
                      readOnly
                      type="text"
                      className="form-control"
                      placeholder="Praza Ramón, 31, 8º F"
                      value={remitente?.direccion}
                      name="remitente direccion"
                    />
                  </div>
                  <div className="mb-3">
                    <label className="form-label-input">Referencia</label>
                    <input
                      readOnly
                      type="text"
                      className="form-control"
                      placeholder="Praza Ramón, 31, 8º F"
                      value={remitente?.referencia}
                      name="remitente referencia"
                    />
                  </div>
                  <div className="mb-3">
                    <label className="form-label-input">Código Postal</label>
                    <input
                      readOnly
                      type="text"
                      className="form-control"
                      placeholder="Cod. postal"
                      name="remitente codigoPostal"
                      value={remitente?.codigoPostal}
                    />
                  </div>
                  <div className="mb-3">
                    <label className="form-label-input">Email</label>
                    <input
                      readOnly
                      type="email"
                      className="form-control"
                      placeholder="example@example.com"
                      name="remitente email"
                      value={remitente?.email}
                    />
                  </div>
                </div>
                <div className="col-lg-6 ">
                  <h5 className="titleSection-form">Destinatario</h5>
                  <hr />

                  <div className="mb-3">
                    <label className="form-label-input">Ruc/Dni</label>
                    <input
                      readOnly
                      type="text"
                      className="form-control"
                      placeholder="110XXXXXXXXX"
                      name="destinatario ruc"
                      value={destinatario?.ruc}
                    />
                  </div>
                  <div className="mb-3">
                    <label className="form-label-input">ID Destinatario</label>
                    <input
                      readOnly
                      type="text"
                      className="form-control"
                      // value="RF45540000001"
                      value={destinatario?.idDestinatario}
                    />
                  </div>
                  <div className="mb-3">
                    <label className="form-label-input">
                      Nombres Completos
                    </label>
                    <input
                      readOnly
                      type="text"
                      className="form-control"
                      placeholder="Nombres Completos"
                      name="destinatario nombres"
                      value={destinatario?.nombres}
                    />
                  </div>
                  <div className="mb-3">
                    <label className="form-label-input">
                      Télefono Principal
                    </label>
                    <input
                      readOnly
                      type="text"
                      className="form-control"
                      placeholder="Apellidos Completos"
                      name="destinatario telefono1"
                      value={destinatario?.telefono1}
                    />
                  </div>
                  <div className="mb-3">
                    <label className="form-label-input">Télefono 1</label>
                    <input
                      readOnly
                      type="text"
                      className="form-control"
                      placeholder="Télefono 1"
                      name="destinatario telefono2"
                      value={destinatario?.telefono2}
                    />
                  </div>
                  <div className="mb-3">
                    <label className="form-label-input">Télefono 2</label>
                    <input
                      readOnly
                      type="text"
                      className="form-control"
                      placeholder="Télefono 2"
                      name="destinatario telefono3"
                      value={destinatario?.telefono3}
                    />
                  </div>
                  <div className="mb-3">
                    <label className="form-label-input">Provincia</label>
                    <input
                      readOnly
                      type="text"
                      className="form-control"
                      placeholder="Loja"
                      value={destinatario?.provincia}
                      name="remitente provincia"
                      required
                    />
                  </div>
                  <div className="mb-3">
                    <label className="form-label-input">Cantón</label>
                    <input
                      readOnly
                      type="text"
                      className="form-control"
                      placeholder="Loja"
                      value={destinatario?.canton}
                      name="remitente canton"
                    />
                  </div>
                  <div className="mb-3">
                    <label className="form-label-input">Parroquia</label>
                    <input
                      readOnly
                      type="text"
                      className="form-control"
                      placeholder="Loja"
                      value={destinatario?.parroquias}
                      name="remitente canton"
                    />
                  </div>
                  <div className="mb-3">
                    <label className="form-label-input">Dirección</label>
                    <input
                      readOnly
                      type="text"
                      className="form-control"
                      placeholder="Praza Ramón, 31, 8º F"
                      value={destinatario?.direccion}
                      name="destinatario direccion"
                      required
                    />
                  </div>
                  <div className="mb-3">
                    <label className="form-label-input">Referencia</label>
                    <input
                      readOnly
                      type="text"
                      className="form-control"
                      placeholder="Praza Ramón, 31, 8º F"
                      value={destinatario?.referencia}
                      name="destinatario referencia"
                    />
                  </div>
                  <div className="mb-3">
                    <label className="form-label-input">Código Postal</label>
                    <input
                      readOnly
                      type="text"
                      className="form-control"
                      placeholder="Cod. Postal"
                      name="destinatario codigoPostal"
                      value={destinatario?.codigoPostal}
                    />
                  </div>
                  <div className="mb-3">
                    <label className="form-label-input">Email</label>
                    <input
                      readOnly
                      type="email"
                      className="form-control"
                      placeholder="example@example.com"
                      name="destinatario email"
                      value={destinatario?.email}
                      required
                    />
                  </div>
                </div>
              </div>
              <div className="div">
                <h5 className="titleSection-form">Informacion Adicional</h5>
                <hr />
                <div className="row">
                  <div className="col-lg-6 ">
                    <div className="mb-3">
                      <label className="form-label-input">Tipo</label>
                      <input
                        readOnly
                        className="form-control"
                        type="text"
                        placeholder=""
                        min={0}
                        value={tipo}
                        name="tipo"
                      />
                    </div>
                    <div className="mb-3">
                      <label className="form-label-input">
                        Número de Pienzas
                      </label>
                      <input
                        readOnly
                        className="form-control"
                        type="number"
                        placeholder="1"
                        min={0}
                        value={numPiezas}
                        name="numPiezas"
                      />
                    </div>
                    <div className="mb-3">
                      <label className="form-label-input">Contenido</label>
                      <textarea
                        className="form-control"
                        rows="5"
                        placeholder="Describe el contenido del producto"
                        value={contenido}
                        name="contenido"
                        readOnly
                      />
                    </div>
                  </div>

                  <div className="col-lg-6">
                    <form>
                      <div className="mb-3">
                        <label className="form-label-input">Peso</label>
                        <input
                          readOnly
                          type="number"
                          className="form-control"
                          placeholder="0.00 Kg"
                          name="peso pesototal"
                          value={peso?.pesototal}
                        />
                      </div>
                      <div className="mb-3">
                        <label className="form-label-input">Largo</label>
                        <input
                          readOnly
                          className="form-control"
                          type="number"
                          placeholder={0}
                          name="peso largo"
                          value={peso?.largo}
                        />
                      </div>
                      <div className="mb-3">
                        <label className="form-label-input">Ancho</label>
                        <input
                          readOnly
                          className="form-control"
                          type="number"
                          // name="number"
                          placeholder={0}
                          name="peso ancho"
                          value={peso?.ancho}
                        />
                      </div>
                      <div className="mb-3">
                        <label className="form-label-input">Alto</label>
                        <input
                          readOnly
                          className="form-control"
                          type="number"
                          // name="number"
                          placeholder={0}
                          name="peso alto"
                          value={peso?.alto}
                        />
                      </div>

                      <div className="mb-3">
                        <label className="form-label-input">
                          Valor Declarado
                        </label>
                        <input
                          readOnly
                          type="text"
                          className="form-control"
                          value="$ 0.00"
                        />
                      </div>
                      <div className="mb-3">
                        <label className="form-label-input">
                          Valor Asegurado
                        </label>
                        <input
                          readOnly
                          type="text"
                          className="form-control"
                          value="$ 0.00"
                        />
                      </div>
                    </form>
                  </div>
                </div>
              </div>
              <div className="row">
                <div className="col-sm-12 col-md-6 ">
                  <button
                    type="button"
                    onClick={cleanValues}
                    className=" btn-dt-details col mx-1 p-2 bt-a w-100"
                  >
                    Cerrar
                  </button>
                </div>
                <div className="col-sm-12 col-md-6 ">
                  <button
                    type="button"
                    className=" w-100 btn btn-danger"
                    data-bs-toggle="modal"
                    data-bs-target="#anularguia"
                  >
                    Anular Guía
                  </button>
                  <div
                    className="modal fade"
                    id="anularguia"
                    data-bs-backdrop="static"
                    data-bs-keyboard="false"
                    aria-labelledby="staticBackdropLabel"
                    aria-hidden="true"
                  >
                    <div className="modal-dialog modal-dialog-centered">
                      <div className="modal-content">
                        <div className="modal-header">
                          <h5 className="modal-title" id="staticBackdropLabel">
                            Esta seguro de anular la Guía?
                          </h5>
                          <button
                            type="button"
                            className="btn-close"
                            data-bs-dismiss="modal"
                            aria-label="Close"
                          ></button>
                        </div>

                        <div className="modal-body">
                          <p>
                            Una ves anulada la guía no se podra cambiar este
                            estado.
                          </p>
                        </div>

                        <div className="modal-footer">
                          <button
                            type="button"
                            className="btn btn-secondary"
                            data-bs-dismiss="modal"
                          >
                            Cancelar
                          </button>
                          <button
                            type="submit"
                            className=" btn btn-danger p-2"
                            data-bs-dismiss="modal"
                            onClick={handlerAnularGuia}
                          >
                            Anular Guia
                          </button>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        )}

        <Dashfooter />
      </div>
    </div>
  );
}

export default AnularGuias;
