import { useState } from 'react';
import { useDispatch } from 'react-redux';
import { validarEmail } from '../helpers/helper';
import { createUser } from "../redux/actions/auth_actions"
import { useToasts } from "react-toast-notifications";

export const useFormCreateUser = (initialState = {}) => {
    const [values, setValues] = useState(initialState);
    const dispatch = useDispatch();
    const { addToast } = useToasts();


    const reset = () => {
        setValues(initialState);
    }
    const msgCreateOk = () => {
        return addToast("Usuario creado con éxito.", {
            appearance: "success",
            autoDismiss: true,
            autoDismissTimeout: 3000,
        })
    }
    const msgCreateError = () => {
        return addToast("Error al crear usuario.", {
            appearance: "error",
            autoDismiss: true,
            autoDismissTimeout: 3000,
        });
    }

    const handleInputChange = ({ target }) => {
        setValues({
            ...values,
            [target.name]: target.value
        });
    }
    const handlerBlur = () => {
        if (values.password !== values.repassword) {
            setValues({
                ...values,
                errorMsg: "Las contraseñas no coinciden*"
            })
        } else {
            setValues({
                ...values,
                errorMsg: ""
            })
        }
    }
    const handlerBlurEmail = () => {
        console.log("ANTES DE COMPROBAR")
        if (validarEmail(values.email)) {
            setValues({
                ...values,
                errorEmail: ""
            })
        } else {
            setValues({
                ...values,
                errorEmail: "El email no es válido"
            })
        }
    }
    const onClickChangeHandler = () => {
        if (values.errorMsg === "" && values.errorEmail === "") {
            if (values.rol !== "" &&
                values.name !== "",
                values.email !== "",
                values.codigoPostal !== "",
                values.dni !== "",
                values.telefPrincipal !== "",
                values.direccion !== "",
                values.telef1 !== "",
                values.provincia !== "",
                values.telef2 !== "",
                values.canton !== "",
                values.parroquia !== "",
                values.password !== "") {
                // HACER DISPATCH
                let dataUser = {
                    rol: values.rol,
                    nombre: values.nombre.toLowerCase(),
                    email: values.email,
                    codigoPostal: values.codigoPostal,
                    dni: values.dni,
                    telefPrincipal: values.telefPrincipal,
                    direccion: values.direccion,
                    telef1: values.telef1,
                    provincia: values.provincia,
                    telef2: values.telef2,
                    canton: values.canton,
                    parroquia: values.parroquia,
                }
                console.log("ANTES DE DISPATCH")
                console.log(dataUser);
                dispatch(createUser(dataUser, values.password, reset, msgCreateOk, msgCreateError))
            }
        } else {
            // Existen errores
            setValues({
                ...values,
                errorMsg: "Existen Errores en el formulario"
            })
        }


    }

    return [values, handleInputChange, handlerBlur, onClickChangeHandler, handlerBlurEmail];
}