import React from 'react';
import ComponentCarousel from 'react-awesome-component-carousel';
import img from '../../img/banner_1.png'
import img2 from '../../img/banner_2.png'
import img3 from '../../img/banner_3.png'
import "../carrucel/carrucel.css"


function Carucel() {
    const comp1 = () => (
        <div className="comp-1">
            <img className="img-banner" src={img} alt="Rapifas" />
      

        </div>
    );
    const comp2 = () => (
        <div className="comp-2">
            <img className="img-banner" src={img2} alt="Rapifas" />
        </div>
    );
    const comp3 = () => (
        <div className="comp-3">
            <img className="img-banner" src={img3} alt="Rapifas" />
        </div>
    );

    const components = [comp1, comp2, comp3];
    return (
        // <div style={{ width: '100%', height: '100vh', position: 'relative', overflow: 'hidden' }}>
        <div className="carrucel-header">
            <ComponentCarousel
                transition={'fade'}
                transitionDuration={0.4}
                auto
                autoDuration={12}
                coolOff={6}
                components={components}
            />
        </div>
     
    )
}

export default Carucel