import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  actualizarEstadoGuia,
  updateitemListDebaja,
  findGuia,
  currentGuiaDetail,
} from "../redux/actions/guias_actions";

import { useToasts } from "react-toast-notifications";
import { savePDF } from "../helpers/helper";

export const UseFormDardeBajaGuias = () => {
  const dispatch = useDispatch();
  const {
    guias: { listGuias, fetching, msgError, printGuia },
  } = useSelector((state) => state);
  const [values, setValues] = useState({
    busqueda: "guia",
    textSearch: "",
    shoDetail: false,
    showDownload: false,
  });

  const [CurrentGuia, setCurrentGuia] = useState({});

  //mensajes de alerta
  const { addToast } = useToasts();

  const toastSms = (text, type) => {
    return addToast(text, {
      appearance: type,
      autoDismiss: true,
      autoDismissTimeout: 3000,
    });
  };

  const handlerChangeText = (e) => {
    let res = e.target.value.replace(/ /g, "");
    setValues({
      ...values,
      [e.target.name]: res.trim(),
    });
  };

  const getGuiaSearch = () => {
    // console.log('aqui toy');
    const errorMesajje = () => toastSms(`${msgError}`, "error");
    const noEncontrada = () => toastSms(`Guia no encontrada`, "info");
    if (values?.textSearch.trim()) {
      console.log(values?.busqueda, values?.textSearch);
      dispatch(
        findGuia(
          values?.busqueda,
          values?.textSearch,
          errorMesajje,
          noEncontrada
        )
      );
    } else {
      toastSms(`El campo de busqueda esta vacio`, "error");
    }
  };

  const showDetails = (status, guia) => {
    if (guia != null) {
      console.log("contiene guia");
      setValues({
        ...values,
        shoDetail: status,
      });
      setCurrentGuia(guia);
    } else {
      setValues({
        ...values,
        shoDetail: status,
      });
      setCurrentGuia({});
    }
  };

  const darDeBaja = () => {
    const errorMessaje = () => toastSms(`${msgError}`, "error");
    const Success = () => toastSms(`Guia actualizada con exito`, "success");

    let updateStadoGuia = {
      ...CurrentGuia,
      datosBaja: values,
      estado: "de baja",
    };
    dispatch(actualizarEstadoGuia(updateStadoGuia, Success, errorMessaje));
    dispatch(
      updateitemListDebaja(updateStadoGuia, updateStadoGuia.idGuia, listGuias)
    );
  };
  const solicitarAnulacion = () => {
    const errorMessaje = () => toastSms(`${msgError}`, "error");
    const Success = () => toastSms(`Guia actualizada con exito`, "success");

    let updateStadoGuia = {
      ...CurrentGuia,
      estado: "pendiente anulacion",
      motivo: values?.motivoAnulacion,
    };
    dispatch(actualizarEstadoGuia(updateStadoGuia, Success, errorMessaje));
    dispatch(
      updateitemListDebaja(updateStadoGuia, updateStadoGuia.idGuia, listGuias)
    );
    showDetails(false, null);
    clearMotivo();
  };

  const clearMotivo = () => {
    setValues({
      ...values,
      busqueda: "guia",
      textSearch: "",
      shoDetail: false,
    });
  };

  const dowloadGuia = (guia) => {
    setValues({
      ...values,
      showDownload: true,
    });
    setCurrentGuia(guia);
    console.log(CurrentGuia);
    savePDF("current-guia-salve");
    setTimeout(() => {
      setValues({
        ...values,
        showDownload: false,
      });
    }, 2000);
  };
  const printGuiaAction = (guia) => {
    setCurrentGuia(guia);
    console.log(CurrentGuia);
    setTimeout(() => {
      window.print();
    }, 500);
  };

  return [
    values,
    fetching,
    listGuias,
    CurrentGuia,
    darDeBaja,
    solicitarAnulacion,
    handlerChangeText,
    getGuiaSearch,
    showDetails,
    dowloadGuia,
    printGuiaAction,
    // currentPrintGuia,
    // printGuiaNmed,
  ];
};
