import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { v1 as uuid } from "uuid";
import { guiaTest, listTextGuias } from "../constants/constants";
import { localidadesList } from "../constants/db_provincias";
import { savePDF, truncate } from "../helpers/helper";
import { debounce } from "lodash.debounce";

import {
  addGuia,
  guardarGuias,
  limpiarGuia,
  salveToPrint,
  updateGuia,
} from "../redux/actions/guias_actions";
import { useToasts } from "react-toast-notifications";
import { db } from "../firebase";

export function UserFormCrearGuias(initialValues) {
  const {
    guias: { currentEditGuia, guiasAgregadas, msgError, ListPrintGuias },
    auth: { dataProfile },
  } = useSelector((state) => state);
  const dispatch = useDispatch();
  const { addToast } = useToasts();
  const rolUser =
    dataProfile?.rol === "admin"
      ? "Administrador"
      : dataProfile?.rol === "puntoVenta"
      ? "Punto de venta"
      : dataProfile?.rol === "sucursal"
      ? "Sucursal"
      : "Coorporativo";

  const [values, setValues] = useState(initialValues);

  const handledChange = (e) => {
    let name = e.target.name.split(" ");
    if (name[0] === "remitente") {
      let remitente = {
        ...values.remitente,
        [name[1]]: e.target.value,
      };
      setValues({ ...values, remitente: remitente });
    } else if (name[0] === "destinatario") {
      let destinatario = {
        ...values.destinatario,
        [name[1]]: e.target.value,
      };
      setValues({ ...values, destinatario: destinatario });
    } else if (name[0] === "peso") {
      let pesotemp;
      pesotemp = {
        ...values.peso,
        [name[1]]: e.target.value,
      };
      if (values?.pesoVol) {
        let total =
          (parseFloat(pesotemp?.largo ?? 0.0) *
            parseFloat(pesotemp?.ancho ?? 0.0) *
            parseFloat(pesotemp?.alto ?? 0.0)) /
          6000;
        pesotemp = {
          ...pesotemp,
          pesototal: total.toString(),
        };
      }
      setValues({ ...values, peso: pesotemp });
    } else if (name.length === 1) {
      let valuestemp;
      valuestemp = {
        ...values,
        [name[0]]: e.target.value,
      };
      if (name[0] === "tipo") {
        valuestemp = {
          ...values,
          [name[0]]: e.target.value,
          numPiezas: 1,
        };
      } else if (name[0] === "v_declarado") {
        let asegurado = (parseFloat(e.target.value) * 0.005).toString();
        valuestemp = {
          ...values,
          v_declarado: e.target.value,
          v_asegurado: asegurado,
        };
      }
      setValues(valuestemp);
    }
  };
  const clicPesoVol = () => {
    let temva = {
      ...values,
      pesoVol: !values?.pesoVol,
    };
    if (temva?.pesoVol) {
      temva = {
        ...values,
        pesoVol: !values?.pesoVol,
        peso: {
          pesototal: "",
        },
      };
    } else {
      temva = {
        ...values,
        pesoVol: !values?.pesoVol,
        peso: {
          pesototal: "",
          ancho: "",
          alto: "",
          largo: "",
        },
      };
    }
    setValues(temva);
  };
  const SaveGuiaStorage = (e) => {
    const success = () => toastSms("Guias Agregada a la Lista", "success");
    const info = () => toastSms("Guia Editada", "info");
    e.preventDefault();
    if (currentEditGuia?.remitente?.nombres === "") {
      values.idGuia = uuid();
      values.fechaActual = Date.now().toString();
      values.estado = "creado";
      values.creadoPorUsuario = dataProfile?.email;
      values.creadoPor = rolUser?.toString();
      setValues(values);
      dispatch(addGuia(values));
      setValues(initialValues);
      success();
    } else {
      values.idGuia = currentEditGuia.idGuia;
      values.fechaActual = currentEditGuia.fechaActual;
      dispatch(updateGuia(values, values.idGuia, guiasAgregadas));
      setValues(initialValues);
      info();
    }
  };
  const toastSms = (text, type) => {
    return addToast(text, {
      appearance: type,
      autoDismiss: true,
      autoDismissTimeout: 1500,
    });
  };

  const saveGuia = () => {
 
      // try {
        savePDF("guias-export");
        setTimeout(() => {
          window.print();
        }, 2000);
      // } catch (error) {
      //   console.log("====> ERORR AL CREAR P");
      // }

  };

  const setGuiasDb = () => {
    const success = () => toastSms("Guias Agregadas con exito", "success");
    const errorsms = () => toastSms(`${msgError}!`, "error");
    dispatch(guardarGuias(guiasAgregadas, success, errorsms, saveGuia));
  };

  const printLog = () => {
    saveGuia();
  };

  return [
    
    values,
    printLog,
    setGuiasDb,
    clicPesoVol,
    SaveGuiaStorage,
    setValues,
    handledChange,
  ];
}
