//buscar guias por id para agregar al la lista de manifiestos
export const findGuiaManifiesto = (
  idGuia,
  showError,
  comprobarGuia,
  agregaridalalista,
  actualizarPeso
) => {
  return async (dispatch) => {
    dispatch({
      type: GET_GUIA_MANIFIESTO,
    });
    try {
      let resp = await db
        .collection("guias")
        .doc(`${idGuia}`)
        .get()
        .then((doc) => {
          return doc.data();
        });
      // para agregregarse la guia debe de estar en peso actualizado
      if (resp.estado === "peso actualizado") {
        await dispatch({
          type: GET_GUIA_MANIFIESTO_SUCCESS,
          payload: resp,
        });
        agregaridalalista(resp.idGuia);
        comprobarGuia(resp.destinatario.provincia, resp.idGuia);
      } else {
        actualizarPeso();
        dispatch({
          type: GET_GUIA_MANIFIESTO_ERROR,
          payload: "Error al buscar guías",
        });
      }
    } catch (error) {
      showError();
      dispatch({
        type: GET_GUIA_MANIFIESTO_ERROR,
        payload: "Error al buscar guías",
      });
      setTimeout(() => {
        dispatch({
          type: GET_GUIA_MANIFIESTO_ERROR,
          payload: null,
        });
      }, 6000);
    }
  };
};

