export const crearManifiesto = (
  guias,
  manifiesto,
  mostrarAlertaOk,
  mostrarAlertaError
) => {
  return async (dispatch) => {
    dispatch({
      type: CREATE_MANIFIESTO_DB,
    });
    try {
      await Promise.all(
        guias.map(async (guia) => {
          await db
            .collection("guias")
            .doc(`${guia}`)
            .update({
              estado: "agregado manifiesto",
              fechaENManifiesto: `${manifiesto.fechaCreacion}`,
              agregadoEnManifiestoPor: `${manifiesto.manifiestoCreadoPor}`,
              manifiestoId: `${manifiesto.idManifiesto}`,
            });
        })
      );
      await db
        .collection("manifiestos")
        .doc(`${manifiesto.idManifiesto}`)
        .set(manifiesto);

      dispatch({
        type: CREATE_MANIFIESTO_DB_SUCCESS,
      });
      mostrarAlertaOk();
    } catch (error) {
      dispatch({
        type: CREATE_MANIFIESTO_DB_ERROR,
      });
      mostrarAlertaError();
    }
  };
};
//resepcion de manifiestos
export const findManifiesto = (idManifiesto, successSms, errorSms) => {
  return async (dispatch) => {
    dispatch({
      type: GET_MANIFIESTO_DB,
    });
    try {
      await db
        .collection("manifiestos")
        .doc(`${idManifiesto}`)
        .get()
        .then((doc) => {
          if (doc.exists) {
            dispatch({
              type: GET_MANIFIESTO_DB_SUCCESS,
              payload: doc.data(),
            });
            successSms();
          } else {
            dispatch({
              type: GET_MANIFIESTO_DB_ERROR,
              payload: "No existen manifiestos con ese id",
            });
          }
        });
    } catch (error) {
      errorSms();
      dispatch({
        type: GET_MANIFIESTO_DB_ERROR,
        payload: "Error buscar manifiesto",
      });
      setTimeout(() => {
        dispatch({
          type: GET_MANIFIESTO_DB_ERROR,
          payload: null,
        });
      }, 6000);
    }
  };
};

export const updateGuiaStatus = (ListGuias, idGuia, item) => {
  let tempList = ListGuias.map((guia) => {
    if (guia.idGuia === idGuia) {
      return item;
    } else {
      return guia;
    }
  });

  let listResividos = tempList.filter((item) =>
    item.estado.includes("receptado")
  ).length;
  let malEncamidanos = tempList.filter((item) =>
    item.estado.includes("mal encaminados")
  ).length;
  let pendientes = tempList.filter((item) =>
    item.estado.includes("creado")
  ).length;

  return (dispach) => {
    dispach({
      type: UPDATE_STATUS_GUIA_MANIFIESTO,
      payload: tempList,
    });
    dispach({
      type: SET_COMPROBACION_ENVIOS_RECINIDOS,
      payload: listResividos,
    });
    dispach({
      type: SET_COMPROBACION_ENVIOS_MAL_ENCAMINADOS,
      payload: malEncamidanos,
    });
    dispach({
      type: SET_COMPROBACION_ENVIOS_PENDIENTES,
      payload: pendientes,
    });
  };
};

