export const setAsignacionCorrespondencia = (
  asignacion,
  listIdGuias,
  msgError,
  msgSuccess
) => {
  return async (dispatch) => {
    dispatch({
      type: SET_ASIGNACION_DB,
    });
    try {
      await Promise.all(
        listIdGuias.map(async (guia) => {
          await db.collection("guias").doc(`${guia}`).update({
            estado: "asignado a cartero",
            estadoAsignado: "asignado",
          });
        }),
        await db
          .collection("asignaciones")
          .doc(`${asignacion.idAsignacion}`)
          .set(asignacion)
      );

      dispatch({
        type: SET_ASIGNACION_DB_SUCCESS,
      });
      msgSuccess();
    } catch (error) {
      dispatch({
        type: SET_ASIGNACION_DB_ERROR,
        errorSms: error,
      });
      msgError();
    }
  };
};
