import {
    GET_USERS,
    GET_USERS_ERROR,
    GET_USERS_SUCCESS,
    CURRENT_USER,
    DELETE_USER,
    DELETE_USER_ERROR,
    DELETE_USER_SUCCESS
} from "../../types/types";

const initialState = {
    fetching: false,
    msgError: null,
    currentUser: {
        canton: "Loja",
        codigoPostal: "",
        direccion: "",
        dni: "",
        email: "",
        nombre: "",
        parroquia: "",
        provincia: "Loja",
        rol: "",
        telef1: "",
        telef2: "",
        telefPrincipal: "",
    },
    listUsers: [],
    // currentEditGuia: null,
};

export let users_reducer = (state = initialState, action) => {
    switch (action.type) {
        case GET_USERS:
            return {
                ...state,
                fetching: true,
                msgError: null
            }
        case GET_USERS_SUCCESS:
            return {
                ...state,
                fetching: false,
                msgError: null,
                listUsers: action.payload
            }
        case GET_USERS_ERROR:
            return {
                ...state,
                fetching: false,
                msgError: action.payload
            }
        case CURRENT_USER:
            return {
                ...state,
                currentUser: action.payload
            }
        case DELETE_USER:
            return {
                ...state,
                fetching: true,
                msgError: null
            }
        case DELETE_USER_SUCCESS:
            return {
                ...state,
                fetching: false,
                msgError: null,
                listUsers: state.listUsers.filter((user) => user.id === action.payload)
            }
        case DELETE_USER_ERROR:
            return {
                ...state,
                fetching: false,
                msgError: action.payload
            }
        default:
            return state;
    }
};
