import {
    GET_USERS,
    GET_USERS_ERROR,
    GET_USERS_SUCCESS,
    CURRENT_USER,
    DELETE_USER,
    DELETE_USER_ERROR,
    DELETE_USER_SUCCESS,
} from '../../types/types'
import { auth, db } from '../../firebase';


export const consultarUsuario = (campo, value) => {
    return async (dispatch) => {
        dispatch({
            type: GET_USERS
        })
        try {
            // let resp;
            const userList = []
            console.log("====> Campo a buscar");
            console.log(campo)
            // if (campo === "nombre") {
            //     resp = await db.collection("usuarios").orderBy("nombre").startAt(value).endAt(value + "uf8ff").get();
            // } else {
            let resp = await db.collection("usuarios").where(campo, "==", value.toLowerCase()).get();
            // }
            resp.docs.forEach((element) => {
                console.log("==> Respuesta al buscar usuarios");
                console.log(element.data())
                let temp = element.data();
                temp.id = element.id;
                userList.push(temp)
            })
            console.log("====> LISTA FINAL DE USUARIOS");
            console.log(userList);
            dispatch({
                type: GET_USERS_SUCCESS,
                payload: userList
            })
        } catch (error) {
            console.log("=====> Error Al buscar usuario");
            console.log(error);
            dispatch({
                type: GET_USERS_ERROR,
                payload: "Error al Buscar usuario"
            })
        }

    }
}

