import React from 'react';

const ComponentCotizacion = ({ title, total, handleChangeCalculo }) => {
    return (
        <>
            {
                (title === "Documento") ?
                    <div className="col-12 pricing-contianer">
                        <div className='row d-flex flex-wrap align-items-center container-precio'>
                            <div className="col-md-4 ">
                                <div className='title-price'><span id="text-btn">Cotizar</span></div>
                            </div>
                            <div className="col-md-8 price">
                                <h5>$ {total.toFixed(2)}</h5>
                            </div>
                        </div>
                    </div>
                    : (title === "Paquete") ? <>
                        <div className='row d-flex flex-wrap align-items-center end-section border-input'>
                            <div className="col-md-6">
                                <div className="form-group first">
                                    <label className="laber-input" htmlFor="fname">Número de Piezas</label>
                                    <input type="number" className="form-control input-content" placeholder="Ingresa un Número" id="fname" />
                                </div>
                            </div>
                            <div className="col-md-6">
                                <div className="form-group first">
                                    <label className="laber-input" htmlFor="fname">Peso (Kg)</label>
                                    <input type="number" min={0} className="form-control input-content" placeholder="Ingresa el peso" id="fname"
                                        onChange={handleChangeCalculo}
                                    />
                                </div>
                            </div>
                        </div>
                        <div className="col-12 pricing-contianer">
                            <div className='row d-flex flex-wrap align-items-center container-precio'>
                                <div className="col-md-4 ">
                                    <div className='title-price'><span id="text-btn">Costo:</span></div>
                                </div>
                                <div className="col-md-8 price">
                                    <h5>$ {total.toFixed(2)}</h5>
                                </div>
                            </div>
                        </div>
                        {/* <div className="col-12">
                                <button className='btn btn-warning btn-cotizar'><span id="text-btn">Cotizar</span></button>
                            </div> */}
                    </>
                        : <></>
            }
        </>
    );
}


export default ComponentCotizacion;